import backend from './endpoint';

// eslint-disable-next-line import/prefer-default-export
export const getAffiliateStats = async walletAddress => backend.http.get(`/api/futures/affiliates/stats?user_address=${walletAddress}`);
export const affiliateRelease = async (userAddress, tokenAddress) => backend.http.post('/api/futures/affiliates/release-commission', {
  user_address: userAddress,
  token_address: tokenAddress,
});



