<template>
  <div class="the-history">
    <transition-content>
      <div class="the-history__wrapper">
        <the-history-table
          :history="historyPage"
          class="the-history__table"
          :class="{ 'is-loading': historyPageLoading }"
          isAffiliate
        />

        <!--         -->
        <div
          class="the-history__preloader"
          v-if="historyPageLoading"
        >
          <e-preloader />
        </div>
      </div>
    </transition-content>
  </div>
</template>

<script>
/**
 * Component:     
 *
 * @version 1.0.2
 */
import { mapGetters } from 'vuex';

import { getAffiliateHistoryPage } from '@/api/backend';

// Components
import EButtonTabs from '@/components/EButtonTabs';
import EPagination from '@/components/EPagination';
import EPreloader from '@/components/EPreloader';
import TheHistoryTable from '@/containers/blocks/TheHistoryTable';

// Transitions
import TransitionContent from '@/containers/transitions/TransitionContent';

export default {
  name: 'the-history',

  data() {
    return {
      historyPage: [],
      // activeTab: TAB_FUTURES_TRADING,
      historyPageLoading: false,
    };
  },

  computed: {
    ...mapGetters({
      activeToken: 'tokens/activeToken',
      userAddress: 'wallets/activeWalletAddress',
      theme: 'ui/getTheme',
    }),
  },

  watch: {
    activeTab: 'fetchFirstPage',
    userAddress: 'fetchFirstPage',
  },

  created() {
    this.fetchFirstPage();
  },

  methods: {
    fetchFirstPage() {
      this.fetchPage();
    },

    async fetchPage() {
      this.historyPageLoading = true;

      const { pageData } = await getAffiliateHistoryPage({
        userAddress: this.userAddress,
        pageNumber: 0,
        activeToken: this.activeToken,
      });

      this.historyPage = pageData;

      this.historyPageLoading = false;
    },
  },

  components: {
    EButtonTabs,
    EPagination,
    EPreloader,
    TheHistoryTable,
    TransitionContent,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-history {
  $this: &;

  margin: 36px 0 22px 0;

  &__wrapper {
    display: flex;
    flex-flow: column;
  }

  &__table {
    min-width: 800px;
    flex: 1;
    transition: opacity 0.5s ease-in-out;

    &.is-loading {
      opacity: 0.2;
    }
  }

  &__tabs {
    margin-bottom: 18px;
    flex: none;
  }

  &__pagination {
    margin-top: 24px;
    margin-bottom: 28px;
    flex: none;
  }

  &__preloader {
    position: absolute;
    top: 0;
    left: 0;
    right: 10px;
    bottom: 0;
  }

  &__dark {
    .e-button-tabs_default .e-button-tabs__item {
      background: #1A1A1A;
      color: #8D9098;

      &:hover {
        color: #fff;
      }

      &.is-active {
        background: #232323;
        color: #fff;

        &:hover {
          color: #fff;
        }
      }
    }

    .the-history-table__heading {
      color: $color-dark-grey;
    }

    .e-history-table-item {
      background: #232323;

      &:hover {
        background: #393939;
      }
    }

    .e-history-table-item__operation-icon_sell,
    .e-history-table-item__operation-icon_buy {
      svg path:nth-child(2) {
        fill: #232323;
      }
    }

    .e-history-table-item__amount,
    .e-history-table-item__price,
    .e-history-table-item__net-amount,
    .e-history-table-item__number {
      color: #8D9098;
    }

    .e-history-table-item__amount .e-big-number__leading-zeros,
    .e-history-table-item__price .e-big-number__leading-zeros,
    .e-history-table-item__net-amount .e-big-number__leading-zeros,
    .e-history-table-item__combiner {
      color: #6a6b70;
    }

    .e-history-table-item__fees,
    .e-history-table-item__gas-fees {
      color: #DA4830;

      &.isZero {
        color: #838383;

        .e-big-number__leading-zeros {
          color: #838383;
        }
      }
    }

    .e-history-table-item__fees .e-big-number__leading-zeros,
    .e-history-table-item__gas-fees .e-big-number__leading-zeros {
      color: #7e3529;
    }

    .e-hint-adress,
    .e-history-table-item__hint {
      background: #232323;
      color: #8D9098;
      box-shadow: 0 3px 10px rgba(0, 0, 0, 0.14);
    }

    .the-history-table__empty {
      background: #232323;
    }

    .e-panel-error__icon svg g path:nth-child(1) {
      fill: #474a4f;
    }
  }
}
</style>



