<template>
  <div
    v-if="contracts && contracts.length > 0 && selectedContract !== null"
    v-click-outside="() => { dropdown = false }"
    class="e-contract-select"
  >
    <div class="e-contract-select__placeholder">
      <div class="e-contract-select__wrapper">
        <img
          :src="makeBlockie(selectedContract.contractAddress)"
          class="e-contract-select__image"
        >
        <div class="e-contract-select__body">
          <div class="e-contract-select__hash">{{shortenAddress(selectedContract.contractAddress, 21, 5)}}</div>
          <div
            class="e-contract-select__status"
            :class="[
              selectedContract.isCurrent ? 'e-contract-select__status_current' : 'e-contract-select__status_outdated',
            ]"
          >
            {{selectedContract.isCurrent ? 'Current' : 'Outdated'}}
          </div>
          <div v-if="!selectedContract.isCurrent" class="e-contract-select__message">
            Please update
            <span class="e-contract-select__hint">
              <e-hint
                :border="true"
              >
                Text
              </e-hint>
            </span>
          </div>
          <div class="e-contract-select__btns">
            <a :href="selectedContract.url" target="_blank" class="e-contract-select__link">
              <e-icon :src="iconLink"/>
            </a>
            <div
              class="e-contract-select__toggler"
              :class="[dropdown ? 'is-active' : '']"
              @click="showDropdown"
            >
              <e-icon :src="iconArrow"/>
            </div>
          </div>
        </div>
      </div>
    </div>
    <transition-dropdown>
      <div class="e-contract-select__dropdown" v-if="dropdown">
        <div class="e-contract-select__list">

          <div class="e-contract-select__list-items">
            <!-- BEGIN:     -->
            <div
              v-for="item in contracts"
              class="e-contract-select__item"
              :class="{ 'is-selected': item.id === selected }"
              :key="item.id"
              @click="select(item.id)"
            >
              <div class="e-contract-select__wrapper">
                <img
                  :src="makeBlockie(item.contractAddress)"
                  class="e-contract-select__image"
                >
                <div class="e-contract-select__body">
                  <div class="e-contract-select__hash">{{shortenAddress(item.contractAddress, 21, 5)}}</div>
                  <div class="e-contract-select__status"
                    :class="[
                      item.isCurrent ? 'e-contract-select__status_current' : 'e-contract-select__status_outdated',
                    ]"
                  >
                    {{item.isCurrent ? 'Current' : 'Outdated'}}
                  </div>
                  <div
                    v-if="item.createdAt"
                    class="e-contract-select__date"
                  >
                     Deployed: {{formatDeployDate(item.createdAt)}}
                  </div>
                  <div class="e-contract-select__marker">
                    <e-icon :src="iconCheckMarker"/>
                  </div>
                </div>
              </div>
            </div>
             <!-- END:     -->
          </div>
        </div>
      </div>
    </transition-dropdown>
  </div>
</template>

<script>
/**
 * Component:     
 *
 * @version 1.0.2
 */

import moment from 'moment';
import makeBlockie from 'ethereum-blockies-base64';

import shortenAddress from '@/util/shortenAddress';

// Components
import EIcon from '@/components/EIcon';
import TransitionDropdown from '@/containers/transitions/TransitionDropdown';
import EHint from '@/components/EHint';

// Icons
import iconLink from '@/icons/e-icon_link-site.svg';
import iconArrow from '@/icons/e-icon_arrow_downer.svg';
import iconCheckMarker from '@/icons/e-icon_checkmark-circle.svg';

export default {
  name: 'e-contract-select',

  props: {
    /**
     *    
     */
    contracts: {
      type: Array,
      default: () => [],
    },

    /**
     *     
     */
    value: {
      type: Number,
    },
  },

  data() {
    return {
      dropdown: false,
      selected: this.value,
    };
  },

  computed: {
    selectedContract() {
      return this.contracts && this.contracts.length > 0 && this.selected
        ? this.contracts.find(item => item.id === this.selected)
        : null;
    },
  },

  watch: {
    selected() {
      this.$emit('input', this.selected);
      this.$emit('change', this.selected);
    },
  },

  created() {
    Object.assign(this, {
      iconLink,
      iconArrow,
      iconCheckMarker,

      settings: {
        wheelPropagation: true,
        suppressScrollX: true,
      },
    });
  },

  methods: {
    shortenAddress,

    makeBlockie: v => (v !== null ? makeBlockie(v) : ''),

    select(option) {
      this.selected = option;
      this.dropdown = false;
    },

    showDropdown() {
      this.dropdown = !this.dropdown;
    },

    formatDeployDate(date) {
      return moment(date).format('MMMM DD YYYY').toUpperCase();
    },
  },

  components: {
    EIcon,
    TransitionDropdown,
    EHint,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-contract-select {
  $this: &;
  position: relative;

  &__wrapper {
    display: flex;
  }

  &__placeholder {
    background-color: #fff;
    border-radius: 4px;
    width: 379px;
    box-sizing: border-box;
    padding-top: 11px;
    padding-left: 14px;
    box-shadow: 0 1px 9px rgba(#94A5B4, .22);
    height: 58px;
    position: relative;
    z-index: 1;

    #{$this}__image {
      max-width: 32px;
      max-height: 32px;
      min-width: 32px;
      min-height: 32px;
      margin-right: 10px;
    }

    #{$this}__hash {
      letter-spacing: .86px;
      font-size: 12px;
      margin-left: 2px;
      margin-bottom: 6px;
    }

    #{$this}__status {
      font-size: 10px;
      padding: 2px 5px 0px 5px;
      border-radius: 2px;
      letter-spacing: .67px;
    }

    #{$this}__status_outdated {
      background-color: $color-red;
      color: #fff
    }

    #{$this}__status_current {
      background-color: $color-green;
      color: #fff
    }

    @include r(1279) {
      width: 350px;
    }
  }

  &__item {
    height: 63px;
    box-sizing: border-box;
    padding-top: 18px;
    padding-bottom: 10px;
    padding-left: 4px;
    cursor: pointer;

    &:not(:last-child) {
      border-bottom: 1px solid $color-violet;
    }

    #{$this}__image {
      max-width: 12px;
      max-height: 12px;
      min-width: 12px;
      min-height: 12px;
      margin-right: 10px;
    }

    #{$this}__hash {
      letter-spacing: .79px;
      font-size: 11px;
      margin-bottom: 3px;
    }

    #{$this}__status {
      padding-left: 12px;
      font-size: 11px;
      font-weight: 700;

      &:before {
        content: '';
        position: absolute;
        top: 3px;
        left: 0;
        height: 8px;
        width: 8px;
        border-radius: 50%;
      }
    }

    #{$this}__status_outdated {
      color: $color-red;

      &:before {
        background-color: $color-red;
      }
    }

    #{$this}__status_current {
      color: $color-green;

      &:before {
        background-color: $color-green;
      }
    }

    #{$this}__body {
      padding-left: 2px;
      padding-top: 2px;
      padding-right: 30px;
    }

    &.is-selected {
      #{$this}__marker .e-icon svg {
        fill: $color-blue;
      }
    }
  }

  &__image {
    box-sizing: border-box;
    background-color: black;
    margin-top: 2px;
  }

  &__dropdown {
    position: absolute;
    top: 100%;
    padding: 0px 6px 15px 10px;
    left: 20px;
    right: 5px;
    box-shadow: 0 1px 9px rgba(#94A5B4, .22);
    border-radius: 0 0 4px 4px;
    background-color: #fff;
  }

  &__list {
    max-height: 190px;
    padding-right: 8px;
    overflow-y: auto;

    &::-webkit-scrollbar {
      width: 2px;
      height: 0;
    }

    &::-webkit-scrollbar-track {
      background: $color-violet;
      border-radius: 2px;
    }

    &::-webkit-scrollbar-thumb {
      background: $color-blue;
      border-radius: 2px;
    }
  }

  &__body {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    padding-right: 65px;
  }

  &__hash {
    color: $color-dark-blue;
    font-weight: 800;
    width: 100%;
    line-height: 1;
  }

  &__message {
    font-size: 11px;
    font-weight: 600;
    color: $color-dark-blue;
    margin-left: 8px;
    padding-top: 2px;
  }

  &__status {
    display: inline-block;
    position: relative;
    text-transform: uppercase;
  }

  &__date {
    font-size: 11px;
    color: #A5B2BB;
    font-weight: 600;
    margin-left: 5px;
  }

  &__btns {
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
    display: flex;
  }

  &__link {
    width: 19px;
    height: 19px;
    margin-right: 13px;
    .e-icon svg {
      fill: $color-blue;
    }
  }

  &__hint {
    display: inline-block;
    margin-left: 2px;
  }

  &__toggler {
    width: 11px;
    height: 6px;
    margin-top: 6px;
    cursor: pointer;
    position: relative;
    transform: rotate(0);
    transition: $transition-time;

    .e-icon svg {
      fill: #CFCFDE;
      transition: $transition-time;
    }

    &:before {
      content: '';
      position: absolute;
      top: -10px;
      left: -10px;
      right: -10px;
      bottom: -10px;
    }

    &.is-active {
      transform: rotate(180deg);

      .e-icon svg {
        fill: $color-blue;
      }
    }
  }

  &__marker {
    position: absolute;
    top: 6px;
    right: 13px;
    width: 16px;
    height: 16px;

    .e-icon svg {
      fill: $color-grey-second;
    }
  }
}

</style>



