<script>
/**
 * Component:    #3
 *
 * @version 2.0.0
 */

// import { Carousel, Slide } from 'vue-carousel';
import debounce from 'lodash/debounce';
import throttle from 'lodash/throttle';
import { mapGetters } from 'vuex';
// import getIntercom from '@/util/getIntercom';
import 'swiper/dist/css/swiper.css';
import { swiper, swiperSlide } from 'vue-awesome-swiper';

import { chatMixin, routesMixin } from '@/util/mixins';
import { getStats } from '@/api/backend/statistics';
import log from '@/util/logger';
import canUseWebP from '@/util/canUseWebP';

// Components
import EBigNumber from '@/components/EBigNumber';
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';
import TheFooter from '@/containers/blocks/TheFooter';
import TheHeaderInner from '@/containers/blocks/TheHeaderInner';
import TheHeaderNew from '@/containers/blocks/TheHeaderNew';
import ThePopup from '@/containers/blocks/ThePopup';
import TheLogin from '@/containers/blocks/TheLogin';
import TheHowItWorks from '@/containers/blocks/TheHowItWorks';
import TheLandingSmartContract from '@/containers/blocks/TheLandingSmartContract';
import TheLatestBlogPosts from '@/containers/blocks/TheLatestBlogPosts';
// import TheWelcomeDmex from '@/containers/blocks/TheWelcomeDmex';

import iconMargin from '@/icons/e-icon__margin.svg';
import iconArrowMore from '@/icons/e-icon__arrow-more.svg';
import iconDecentralized from '@/icons/e-icon__decentralized.svg';
import iconAnon from '@/icons/e-icon__anon.svg';
import iconRun from '@/icons/e-icon__run.svg';
import iconDmex from '@/icons/e-icon__dmex.svg';
import iconWave from '@/icons/e-icon__wave.svg';
import iconDmexBg from '@/icons/e-icon__dmex-bg.svg';

const SWIPER_OPTIONS = {
  slidesPerView: 'auto',
  centeredSlides: true,
  spaceBetween: 20,
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },
};

// TODO: wait TheMobileTokenSearch (mobile only)
// TODO: code-splitting for mobile & desktop
// TODO: do something with TheHeaderInner

export default {
  name: 'page-index-landing',

  data() {
    return {
      isMenuOpen: false,
      isStickyHeader: false,
      animatedBlocks: [],
      volume24h: null,
      countWallets: null,
      adaptiveVersionInfoList: false,
      adaptiveVersionOffers: false,
      canUseWebP,

      // Icons
      iconMargin,
      iconDecentralized,
      iconAnon,
      iconArrowMore,
      iconRun,
      iconDmex,
      iconWave,
      iconDmexBg,
    };
  },

  computed: {
    ...mapGetters({
      isPopupShowing: 'ui/isPopupShowing',
      windowSize: 'ui/windowSize',
    }),

    loadFinished() {
      return (
        this.$store.getters['trades/isVolumesInitSuccess'] &&
        this.$store.state.wallets.totalWalletsLoaded &&
        this.$store.state.tokens.tokensLoaded
      );
    },
  },

  created() {
    this.onResize = debounce(this.onResize.bind(this), 200);
    this.SWIPER_OPTIONS = SWIPER_OPTIONS;
  },

  async mounted() {
    this.init();
    this.onResize();
    this.$ga.page(this.$router);
    // this.showTickerTradingView();

    const getAnimatedBlocks = setInterval(() => {
      this.animatedBlocks = document.querySelectorAll('.page-index-landing__animate-block');

      if (this.animatedBlocks.length > 0) {
        this.animatedBlocks = Array.from(this.animatedBlocks);
        clearInterval(getAnimatedBlocks);
        this.checkAnimated();
      }
    }, 100);

    this.handleScroll();
    window.addEventListener('scroll', throttle(this.handleScroll, 120));

    try {
      const { data: { data: { count_wallets: countWallets, volume_24h: volume24h } } } = await getStats();
      this.countWallets = countWallets;
      this.volume24h = volume24h;
    } catch (e) {
      log.error(e);
    }
  },

  destroyed() {
    window.removeEventListener('scroll', this.handleScroll);
  },

  methods: {
    /**
     *     
     *
     * @returns {Promise<void>}
     */
    async init() {
      this.$store.dispatch('trades/loadVolumes');
      this.$store.dispatch('trades/listenToTradeVolumesChange');
      this.$store.dispatch('wallets/loadTotalWallets');
      this.$store.dispatch('tokens/loadTokens');
    },

    handleScroll() {
      if (window.scrollY > 140) {
        this.isStickyHeader = true;
      } else {
        this.isStickyHeader = false;
      }

      this.checkAnimated();
    },

    checkAnimated() {
      if (this.animatedBlocks.length > 0) {
        this.animatedBlocks.forEach((item, index) => {
          if (this.isInViewport(item)) {
            item.classList.add('animated');

            this.animatedBlocks.splice(index, 1);
          }
        });
      }
    },

    isInViewport(elem) {
      const bounding = elem.getBoundingClientRect();
      return (
        bounding.top >= 0 &&
        bounding.top + 70 <= (window.innerHeight || document.documentElement.clientHeight)
      );
    },

    showChat() {
      // getIntercom()('show');
    },

    showTickerTradingView() {
      const tickerScript = document.createElement('script');
      tickerScript.innerHTML = `{
    "symbols": [
      {
        "title": "BTC/USD",
        "proName": "BITSTAMP:BTCUSD"
      },
      {
        "title": "ETH/USD",
        "proName": "BITSTAMP:ETHUSD"
      },
      {
        "title": "LTC/USD",
        "proName": "BITSTAMP:LTCUSD"
      },
      {
        "title": "XRP/USD",
        "proName": "BITSTAMP:XRPUSD"
      },
      {
        "title": "BCH/USD",
        "proName": "BITSTAMP:BCHUSD"
      }
    ],
    "colorTheme": "dark",
    "isTransparent": false,
    "displayMode": "adaptive",
    "locale": "en"
  }`;

      tickerScript.setAttribute('src', 'https://s3.tradingview.com/external-embedding/embed-widget-ticker-tape.js');

      const setTicker = setInterval(() => {
        const tickerTapeEl = document.getElementById('tvTickerTape');
        if (tickerTapeEl) {
          document.getElementById('tvTickerTape').appendChild(tickerScript);
          clearInterval(setTicker);
        }
      }, 400);
    },

    toggleMenu() {
      this.isMenuOpen = !this.isMenuOpen;
    },

    onResize() {
      this.adaptiveVersionInfoList = this.windowSize.width < 1200;
      this.adaptiveVersionOffers = this.windowSize.width < 1020;
    },
  },

  watch: {
    windowSize: {
      deep: true,
      handler() {
        this.onResize();
      },
    },
  },

  mixins: [chatMixin, routesMixin],

  components: {
    swiper,
    swiperSlide,
    // TheWelcomeDmex,
    TheLogin,
    EBigNumber,
    EButton,
    EIcon,
    TheFooter,
    TheHeaderInner,
    TheHeaderNew,
    ThePopup,
    TheHowItWorks,
    TheLandingSmartContract,
    TheLatestBlogPosts,
  },
};
</script>

