    }
  }

  &__section-heading-title {
    font-size: 36px;
    font-weight: 500;
    color: #112271;
    margin: 0;

    .green {
      color: #00DB9C;
    }

    @include r(1200) {
      text-align: center;
    }
  }

  &__offers-list {
    display: flex;
    margin: 24px 0 0;

    .VueCarousel {
      #{$this}__offers-list-item {
        padding-bottom: 24px;
      }
    }

    .VueCarousel-dot-container {
      margin-top: 0 !important;
    }

    .VueCarousel-navigation {
      position: absolute;
      top: -70px;
      right: 18px;
      width: 140px;

      .VueCarousel-navigation-button {
        /*top: calc(50% - 45px);*/
      }

      .VueCarousel-navigation-prev {
        transform: rotate(180deg) !important;
        left: -10px;
      }

      .VueCarousel-navigation-next {
        right: -10px;
      }
    }

    .VueCarousel-pagination {
      position: absolute;
      top: -65px;
      right: 53px;
      width: 70px !important;

      button {
        margin-top: 0 !important;
        background-color: #c9c9d4 !important;

        &.VueCarousel-dot--active {
          background-color: #fff !important;
        }

        svg {
          display: none;
        }
      }
    }
  }

  &__offers-list-bg {
    max-width: 1250px;
    width: 100% !important;
    min-width: 650px !important;
    height: auto !important;
    font-size: 0;
    position: absolute !important;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 17%);

    @include r(640) {
      transform: translate(-50%, 12%);
    }

    svg {
      position: relative !important;
    }
  }

  &__offers-list-item {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 388px;
    margin: 0 auto;

    @include r(820) {
      width: auto;
      max-width: 350px;
    }

    img {
      width: 100%;
      box-shadow: 0 8px 18px 0 rgba(6,10,56,0.10);
      border-radius: 36px;
      overflow: hidden;
    }
  }

  &__info-list {
    width: calc(100% + 20px);
    margin: 35px -10px 0;
    display: flex;
    flex-wrap: wrap;

    @include r(1240) {
      margin-bottom: -20px;
    }
  }

  &__info-list-item {
    flex: 0 0 calc(25% - 20px);
    height: 384px;
    margin: 0 10px;
    background: #040B48;
    border-radius: 30px;
    color: #fff;
    padding: 30px 24px;
    box-sizing: border-box;
    overflow: hidden;
    position: relative;
    transition: all .25s ease-in-out;

    @include r(1199) {
      width: 100%;
      max-width: 380px;
      margin-bottom: 20px;
      margin-left: auto;
      margin-right: auto;
    }

    @include r(670) {
      flex: 0 0 calc(100% - 20px);
    }

    &:hover {
      /*transform: scale(1.05);*/
    }

    &:hover {
      box-shadow: 0 15px 30px 0 rgba(6,10,56,0.30);

      #{$this}__info-list-item-first-screen {
        opacity: 0;
      }

      #{$this}__info-list-item-hover {
        transform: translate(0, -100%);
        opacity: 1;
      }
    }
  }

  &__info-list-item-waves-wrapper {
    overflow: hidden;
    position: absolute;
    z-index: 1;
    bottom: 0;
    left: 0;
    height: 100px;
    width: 100%;
    border-radius: 0 0 30px 30px;
    -webkit-mask-image: -webkit-radial-gradient(white, black);

    &.blue-pale {
      svg .e-icon-wave__path {
        fill: url('#e-icon-wave__linearGradient-blue-pale');
      }

      #{$this}__info-list-item-wave {
        &.wave-1 { animation-delay: 1.5s; }
        &.wave-2 { animation-delay: 3s; }
        &.wave-3 { animation-delay: 4s; }
      }
    }

    &.blue {
      svg .e-icon-wave__path {
        fill: url('#e-icon-wave__linearGradient-blue');
      }

      #{$this}__info-list-item-wave {
        &.wave-1 { animation-delay: 0s; }
        &.wave-2 { animation-delay: 2s; }
        &.wave-3 { animation-delay: 3s; }
      }
    }

    &.red {
      svg .e-icon-wave__path {
        fill: url('#e-icon-wave__linearGradient-red');
      }

      #{$this}__info-list-item-wave {
        &.wave-1 { animation-delay: 3s; }
        &.wave-2 { animation-delay: 1.5s; }
        &.wave-3 { animation-delay: 4.5s; }
      }
    }

    &.green {
      svg .e-icon-wave__path {
        fill: url('#e-icon-wave__linearGradient-green');
      }

      #{$this}__info-list-item-wave {
        &.wave-1 { animation-delay: 4s; }
        &.wave-2 { animation-delay: 5s; }
        &.wave-3 { animation-delay: 6s; }
      }
    }
  }

  &__info-list-item-wave {
    width: 1100px !important;
    height: 170px !important;
    position: absolute !important;
    bottom: -40px;
    opacity: 0;

    &.wave-1 {
      animation: move_wave 5.5s linear infinite;
      bottom: -30px;
    }

    &.wave-2 {
      animation: move_wave 8.5s linear infinite 1.5s;
      bottom: -40px;
    }

    &.wave-3 {
      animation: move_wave 12s linear infinite 3s;
      bottom: -20px;
    }
  }

  &__info-list-item-first-screen {
    opacity: 1;
  }

  &__info-list-item-hover {
    opacity: 0;
  }

  &__info-list-item-first-screen,
  &__info-list-item-hover {
    display: flex;
    flex-direction: column;
    height: 290px;
    padding-bottom: 30px;
    box-sizing: border-box;
    flex-basis: 384px;
    transition: all .3s ease-in-out;
    transform: translate(0, 0);
  }

  &__info-list-item-title {
    font-size: 23px;
    line-height: 23px;
    font-weight: 500;
    color: #fff;
    margin-bottom: 18px;

    .green {
      color: #00DB9C;
    }

    @include r(1250) {
      font-size: 21px;
      line-height: 21px;
    }
  }

  &__info-list-item-icon {
    position: relative;

    &.margin {
      align-self: center;
      width: 227px;
      height: 118px;
      margin-top: auto;
      margin-bottom: 40px;
    }

    &.decentralized  {
      align-self: center;
      width: 180px;
      height: 175px;
      margin-top: auto;
      margin-bottom: 40px;
    }

    &.anon {
      align-self: center;
      width: 107px;
      height: 155px;
      margin-top: auto;
      margin-bottom: 40px;
    }

    &.run {
      align-self: center;
      width: 221px;
      height: 172px;
      margin-top: auto;
      margin-bottom: 40px;
    }

    .e-icon svg {
      top: 0;
    }
  }

  &__info-list-item-readmore {
    display: flex;
    align-items: center;
    font-size: 16px;
    font-weight: 500;
    color: #fff;

    .e-icon {
      width: 8px;
      height: 12px;
      margin-right: 4px;
    }
  }

  &__carousel-review {
    .VueCarousel-slide {
      border-left: 1px solid rgba(#FFF, .4);
      padding: 0 48px;
      box-sizing: border-box;
      margin-left: -1px;

      @include r(767) {
        border-left: none;
        text-align: center;
        padding: 0 20px 0 48px;
      }

      &:first-child {
        border-left: none;
        padding-left: 1px;

        @include r(767) {
          padding: 0 20px 0 48px;
        }
      }

      &:last-child {
        padding-right: 0;

        @include r(767) {
          padding: 0 20px 0 48px;
        }
      }
    }
  }

  &__carousel-review-title {
    font-weight: 500;
    font-size: 20px;
    color: #112271;
    letter-spacing: 0;
    line-height: 26px;
    margin: 0 0 10px;
  }

  &__carousel-review-description {
    font-size: 14px;
    color: #FFFFFF;
    letter-spacing: 0;
    margin: 0;

    .fade {
      opacity: 0.3;
      font-size: 11px;
      color: #112271;
      letter-spacing: 0;
    }
  }

  .e-icon__circle-loading-progress {
    transform: rotate(-90deg) translate(50%, 50%);
    position: absolute;
    top: 50%;
    left: -9px;
  }

  .e-icon__circle-loading-progress__value {
    stroke-dasharray: 339.292;
    stroke-dashoffset: 339.292;
    animation: e-icon__circle-loading-progress 27s 1 linear;
    animation-fill-mode: forwards;
  }

  &__info-list-slider {
    position: relative;
    margin-top: 30px;

    .swiper-container {
      overflow: visible;
    }

    .swiper-slide {
      width: 50%;
      padding-bottom: 30px;

      @include r(599) {
        width: 83%;
        padding-bottom: 10px;
      }
    }

    .swiper-pagination {
      bottom: 9px;
      position: relative;
      z-index: 3;
    }

    .swiper-pagination-bullet {
      width: 5px;
      height: 5px;
      margin: 0 7px;
      transition: .3s ease;
      background-color: #040B48;
      outline: none;
    }

    .swiper-pagination-bullet-active {
      transform: scale(1.3);
    }
  }

  &__animate-block {
    #{$this}__section-heading {
      opacity: .3;
      transform: translate(300px, 0);
      transition: transform .5s ease-in-out, opacity .5s ease-in-out;
    }

    #{$this}__info-list {
      opacity: .3;
      transform: translate(300px, 0);
      transition: transform .5s ease-in-out .08s, opacity .5s ease-in-out .1s;
    }

    &.animated {
      #{$this}__section-heading,
      #{$this}__info-list {
        opacity: 1;
        transform: translate(0, 0);
      }
    }
  }

  @keyframes e-icon__circle-loading-progress {
    from {
      stroke-dashoffset: 339.292;
    }
    to {
      stroke-dashoffset: 0;
    }
  }

  @keyframes move_wave {
    0% {
      transform: translateX(0) translateZ(0) scaleY(1);
      opacity: 0;
    }

    10% {
      opacity: .7;
    }

    50% {
      transform: translateX(-28%) translateZ(0) scaleY(0.75);
    }

    80% {
      opacity: .65;
    }

    100% {
      transform: translateX(-56%) translateZ(0) scaleY(1);
      opacity: 0;
    }
  }
}

</style>



