<template>
  <div class="the-smart-contracts">
    <transition-content>
      <div class="the-smart-contracts__wrapper">
        <div class="the-smart-contracts__heading">
          <e-heading
            title="Smart Contacts"
            title-tag="h1"
            subtitle="Read contract information"
            :icon="iconMyself"
            iconSize="large"
            hasBack
          />
        </div>
        <div class="the-smart-contracts__contracts-box">
          <e-contracts-box />
          <e-contracts-box-futures />
          <e-contracts-box-dmex-oracle />
        </div>
      </div>
    </transition-content>
  </div>
</template>

<script>
/**
 * Component:  
 *
 * @version 1.0.2
 */

import { mapGetters } from 'vuex';

// Components
import EButton from '@/components/EButton';
import EContractsBox from '@/components/EContractsBox';
import EContractsBoxFutures from '@/components/EContractsBoxFutures';
import EContractsBoxDmexOracle from '@/components/EContractsBoxDmexOracle';
import EHeading from '@/components/EHeading';

// Transitions
import TransitionContent from '@/containers/transitions/TransitionContent';

// Icons
import iconMyself from '@/icons/e-icon_smart-contracts.svg';


export default {
  name: 'the-smart-contracts',

  async created() {
    Object.assign(this, { iconMyself });
  },

  metaInfo: {
    title: 'Smart contacts EtherMium.com',
    meta: [
      {
        name: 'description',
        content: 'Do you want details? Learn the smart contracts for decentralized exchange',
      },
    ],
  },

  computed: {
    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

  components: {
    EButton,
    EContractsBox,
    EContractsBoxFutures,
    EContractsBoxDmexOracle,
    EHeading,
    TransitionContent,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-smart-contracts {
  &__contracts-box {
    padding-left: 85px;
    margin: 84px 0 65px;

    @include r(1200) {
      margin: 40px 0 76px;
      padding-left: 28px;
    }

    @include r(640) {
      margin-top: 36px;
      padding-left: 14px;
    }
  }

  &__heading {
    margin-left: 14px;

    @include r(640) {
      margin-left: 0;
    }
  }

  .e-heading__icon {
    @include r(1320) {
      top: 6px;
      left: 54px;
      height: 74px;
      width: 104px;
    }

    @include r(1200) {
      left: 2px;
      height: 65px;
      width: 95px;
    }
  }

  .e-heading__back {
    left: -65px;

    @include r(1320) {
      left: 15px;
    }

    @include r(1200) {
      left: -25px;
    }

    @include r(640) {
      left: -18px;
    }
  }

  &__dark {
    .e-heading__title {
      color: #fff;
    }

    .e-heading__subtitle {
      color: $color-dark-grey;
    }

    .e-contracts-box__circle {
      background: #232323;
      box-shadow: 0 12px 24px rgba(0, 0, 0, 0.14);
    }

    .e-contracts-box__title {
      color: #fff;
    }

    .e-contracts-box__descr {
      color: $color-dark-grey;
    }

    .e-contract-select__placeholder,
    .e-contract-select__dropdown {
      background: #232323;
      box-shadow: 0 1px 9px rgba(0, 0, 0, 0.22);
    }

    .e-contract-select__hash,
    .the-smart-contracts__dark .e-contract-select__hash {
      color: $color-blue-dark-mode;
    }

    .e-contract-select__placeholder .e-contract-select__status_current {
      color: #232323;
    }

    .e-contract-select__toggler .e-icon svg {
      fill: $color-dark-grey;
    }

    .e-contract-select__toggler.is-active .e-icon svg,
    .e-contract-select__item.is-selected .e-contract-select__marker .e-icon svg {
      fill: $color-blue-dark-mode;
    }

    .e-contract-select__marker .e-icon svg {
      fill: $color-dark-grey;
    }

    .e-contract-select__item:not(:last-child) {
      border-color: #393939;
    }

    .e-contract-select__date {
      color: $color-dark-grey;
    }
  }
}
</style>



