<template>
  <div class="the-login">

    <div class="the-login__signin" :class="{ open: authBlock === 'login' }">
      <div class="the-login__preloader" v-if="isLoading">
        <e-preloader />
      </div>

      <div class="the-login__headline">
        <div class="the-login__headline-title">Login</div>
        <div class="the-login__headline-description">Unlock your account</div>
      </div>

      <form class="the-login__form">
        <label class="the-login__form-label" :class="{ 'error': $v.email.$invalid && email, 'success': !$v.email.$invalid }">
          <input type="email" class="the-login__form-input" placeholder="Email" v-model="email" :vuelidate-property="$v.email" @keyup.enter="login" />
          <span class="the-login__form-icon">
            <e-icon :src="iconMail" />
          </span>
        </label>

        <div class="the-login__form-validation" v-if="emailError"><e-icon :src="iconValidation" />{{emailError}}</div>

        <label class="the-login__form-label" :class="{ 'error': $v.password.$invalid && password, 'success': !$v.password.$invalid }">
          <input type="password" class="the-login__form-input" placeholder="Password" v-model="password" :vuelidate-property="$v.password" @keyup.enter="login" />
          <span class="the-login__form-icon">
            <e-icon :src="iconLock" />
          </span>
        </label>

        <div class="the-login__form-validation" v-if="!$v.password.minLength">
          <e-icon :src="iconValidation" />
          Password must have at least 6 letters
        </div>

        <div class="the-login__form-validation" v-if="passwordError">
          <e-icon :src="iconValidation" />
          {{passwordError}}
        </div>

        <button type="button" class="the-login__form-submit" :disabled="$v.email.$invalid || $v.password.$invalid" @click="login">Login</button>

        <div class="the-login__form-to-register">
          <div class="the-login__form-to-register-label">Need an Account?</div>
          <span class="the-login__form-to-register-link" @click="authBlock = 'register'">Register now!</span>
        </div>
      </form>

      <div class="the-login__variants">
        <div class="the-login__variants-label">or Login with</div>
        <div class="the-login__variants-list">
          <router-link :to="{ name: 'signup' }" class="the-login__variants-list-item">
            <div class="the-login__variants-list-icon"><e-icon :src="iconPrivateKey" /></div>
            Private Key
          </router-link>
          <div class="the-login__variants-list-item" @click.prevent="loginWithMetamask">
            <div class="the-login__variants-list-icon"><e-icon :src="iconMetamask" /></div>
            Metamask
          </div>

          <!--<a href="https://dmexhelp.freshdesk.com/support/solutions/articles/47000160693-connecting-ledger-nano-s-or-trezor-wallet" target="_blank" class="the-login__variants-list-item">-->
          <!--<div class="the-login__variants-list-item" @click.prevent="loginWithLedger">-->
          <router-link :to="{ name: 'signup', query: { type: 'ledger' } }" class="the-login__variants-list-item">
            <div class="the-login__variants-list-icon"><e-icon :src="iconLedger" /></div>
            Ledger Nano S
          </router-link>
          <!--</div>-->
          <!--</a>-->
        </div>
      </div>

      <div class="the-login__signin-demo">
        <a href="/f" class="the-login__demo-link" @click="setStorageValue('APP_ENV', 'test')">
          Try Demo Trading

          <e-icon :src="iconArrowDemo" />
        </a>
      </div>
    </div>

    <div class="the-login__signup"  :class="{ open: authBlock === 'register' }">
      <div class="the-login__preloader" v-if="isLoading">
        <e-preloader />
      </div>

      <div class="the-login__headline">
        <div class="the-login__headline-title">Register</div>
        <div class="the-login__headline-description">Create a new Account</div>
      </div>

      <form class="the-login__form">
        <label class="the-login__form-label" :class="{ 'error': $v.emailRegister.$invalid && emailRegister, 'success': !$v.emailRegister.$invalid }">
          <input type="email" class="the-login__form-input" placeholder="Email" v-model="emailRegister" :vuelidate-property="$v.emailRegister" @keyup.enter="register" />
          <span class="the-login__form-icon">
            <e-icon :src="iconMail" />
          </span>
        </label>

        <div class="the-login__form-validation" v-if="emailRegisterError">
          <e-icon :src="iconValidation" />
          {{emailRegisterError}}
        </div>

        <label class="the-login__form-label" :class="{ 'error': $v.passwordRegister.$invalid && passwordRegister, 'success': !$v.passwordRegister.$invalid }">
          <input type="password" class="the-login__form-input" placeholder="Password" v-model="passwordRegister" :vuelidate-property="$v.passwordRegister" @keyup.enter="register" />
          <span class="the-login__form-icon">
            <e-icon :src="iconLock" />
          </span>
        </label>

        <div class="the-login__form-validation" v-if="!$v.passwordRegister.minLength">
          <e-icon :src="iconValidation" />
          Password must have at least 6 letters
        </div>

        <label class="the-login__form-label" :class="{ 'error': $v.passwordRepeatRegister.$invalid && passwordRepeatRegister, 'success': !$v.passwordRepeatRegister.$invalid }">
          <input type="password" class="the-login__form-input" placeholder="Confirm Password" v-model="passwordRepeatRegister" :vuelidate-property="$v.passwordRepeatRegister" @keyup.enter="register" />
          <span class="the-login__form-icon">
            <e-icon :src="iconLock" />
          </span>
        </label>

        <div class="the-login__form-validation" v-if="!$v.passwordRepeatRegister.sameAsPassword">
          <e-icon :src="iconValidation" />
          Passwords should be the same
        </div>

        <div class="the-login__forget">
          <e-icon :src="iconAlert" />
          Make sure you dont forget your password. You will not be able to restore your access if you lose it.
        </div>

        <label class="the-login__form-label">
          <input type="email" class="the-login__form-input" placeholder="Promo Code (optional)" v-model="promoCode" :vuelidate-property="$v.promoCode" @keyup.enter="register" />
          <span class="the-login__form-icon">
            <e-icon :src="iconPromoCode" />
          </span>
        </label>

        <div class="the-login__form-validation form" v-if="registerError">
          <e-icon :src="iconValidation" />
          {{registerError}}
        </div>

        <button type="button" class="the-login__form-submit" :disabled="$v.emailRegister.$invalid || $v.passwordRepeatRegister.$invalid" @click="register">Register</button>

        <div class="the-login__form-to-register">
          <div class="the-login__form-to-register-label">Already have an Account?</div>
          <span class="the-login__form-to-register-link"  @click="authBlock = 'login'">Login</span>
        </div>
      </form>
    </div>

    <div class="the-login__code"  :class="{ open: authBlock === 'verificationCode' }">
      <div class="the-login__preloader" v-if="isLoading">
        <e-preloader />
      </div>

      <div class="the-login__headline">
        <div class="the-login__headline-title">Verify Email</div>
        <div class="the-login__headline-description">
          Please enter the verification code sent to your email:
          <br>
          {{emailRegister}}
        </div>
      </div>

      <form class="the-login__form">
        <label class="the-login__form-label code">
          <input type="number" class="the-login__form-input" v-model="code[0]" @keyup="onCodeKeyUp($event, 0)" @paste.stop="onCodePaste" />
          <input type="number" ref="code1" class="the-login__form-input" v-model="code[1]" @keyup="onCodeKeyUp($event, 1)" @paste.stop="onCodePaste" />
          <input type="number" ref="code2" class="the-login__form-input" v-model="code[2]" @keyup="onCodeKeyUp($event, 2)" @paste.stop="onCodePaste" />
          <input type="number" ref="code3" class="the-login__form-input" v-model="code[3]" @keyup="$refs['code3'].blur()" @paste.stop="onCodePaste" />
        </label>

        <div class="the-login__form-validation verificationCode" v-if="verificationCodeError">
          <e-icon :src="iconValidation" />
          {{verificationCodeError}}
        </div>

        <button type="button" class="the-login__form-submit" :disabled="code.length !== 4" @click="verifyEmail">Confirm</button>

        <div class="the-login__form-to-register">
          <div class="the-login__form-to-register-label">Havent received the email?</div>
          <span class="the-login__form-to-register-link" @click="resendVerificationEmail">Resend email</span>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import CryptoJS from 'crypto-js';
import { mapActions, mapGetters } from 'vuex';
import { validationMixin } from 'vuelidate';
import get from 'lodash/get';
import { email, required, sameAs, minLength } from 'vuelidate/lib/validators';
import { Accounts } from '@/api/ethereum';
import { APP_ETH_NETWORK_ID } from '@/config';
import { TEST_NETWORK_ID, PRIVATE_KEY_TYPE } from '@/util/constants';
import log from '@/util/logger';
import { setStorageValue } from '@/util/storage';

import { faucet } from '@/api/backend/futures/others';
import { register, resendVerificationEmail, verifyEmail, login } from '@/api/backend/login';
import { putGAClientID } from '@/api/backend/ga';

import EIcon from '@/components/EIcon';
import EButton from '@/components/EButton';
import EPreloader from '@/components/EPreloader';

import iconMail from '@/icons/e-icon__mail.svg';
import iconPromoCode from '@/icons/e-icon__promo-code.svg';
import iconLock from '@/icons/e-icon__lock.svg';
import iconPrivateKey from '@/icons/e-icon__private-key.svg';
import iconMetamask from '@/icons/e-icon_metamask-normal.svg';
import iconLedger from '@/icons/e-icon__ledger-logo-small.svg';
import iconValidation from '@/icons/e-icon__validation-incorrect.svg';
import iconAlert from '@/icons/e-icon__alert.svg';
import iconArrowDemo from '@/icons/e-icon__arrow-demo.svg';

export default {
  name: 'the-login',

  data() {
    return {
      setStorageValue,
      authBlock: 'login',
      isLoading: false,
      email: '',
      password: '',
      emailRegister: '',
      passwordRegister: '',
      passwordRepeatRegister: '',
      promoCode: '',
      code: [],
      emailError: '',
      passwordError: '',
      emailRegisterError: '',
      registerError: '',
      verificationCodeError: '',

      // Icons
      iconMail,
      iconPromoCode,
      iconLock,
      iconPrivateKey,
      iconMetamask,
      iconLedger,
      iconValidation,
      iconAlert,
      iconArrowDemo,
    };
  },

  validations: {
    email: {
      email,
      required,
    },

    password: {
      required,
      minLength: minLength(6),
    },

    emailRegister: {
      email,
      required,
    },

    passwordRegister: {
      required,
      minLength: minLength(6),
    },

    passwordRepeatRegister: {
      required,
      minLength: minLength(6),
      sameAsPassword: sameAs('passwordRegister'),
    },

    promoCode: {
      minLength: minLength(4),
    },
  },

  computed: {
    ...mapGetters({
      contractAddress: 'contracts/activeMainContractAddress',
      referrerAddress: 'affiliate/referrerAddress',
    }),
  },

  methods: {
    ...mapActions({
      setActiveWallet: 'wallets/setActiveWallet',
    }),

    async register() {
      if (!this.$v.emailRegister.$invalid && !this.$v.passwordRepeatRegister.$invalid) {
        this.emailRegisterError = '';
        this.registerError = '';
        this.emailError = '';
        this.passwordError = '';
        this.verificationCodeError = '';
        this.isLoading = true;

        const wallet = Accounts.createAccount();
        if (APP_ETH_NETWORK_ID === TEST_NETWORK_ID) {
          faucet({ userAddress: wallet.address, contractAddress: this.contractAddress });
        }

        const encryptedPrivateKey = CryptoJS.AES.encrypt(wallet.privateKey, this.passwordRepeatRegister).toString();

        try {
          const cid = window.ga && window.ga.getAll() && window.ga.getAll()[0] ? window.ga.getAll()[0].get('clientId') : undefined;
          await register({ email: this.emailRegister, encryptedPrivateKey, walletAddress: wallet.address, promoCode: this.promoCode, ref: this.referrerAddress, cid });
          putGAClientID({ walletAddress: wallet.address, clientID: cid, ga: this.$ga, ref: this.referrerAddress, login_type: 'privateKey' });
          this.authBlock = 'verificationCode';

          this.$ga.event({
            eventCategory: 'registration',
            eventAction: 'register',
          });
        } catch (e) {
          this.registerError = get(e, 'response.data.errors.message');
        }

        this.isLoading = false;
      }
    },

    async resendVerificationEmail() {
      this.isLoading = true;
      try {
        await resendVerificationEmail({ email: this.emailRegister });
      } catch (e) {
        log.error(e);
      }
      this.isLoading = false;
    },

    async verifyEmail() {
      if (this.code.length === 4) {
        this.isLoading = true;
        this.verificationCodeError = '';

        try {
          await verifyEmail({ email: this.emailRegister, verificationCode: `${this.code.join('')}` });
          this.authBlock = 'login';

          this.$ga.event({
            eventCategory: 'registration',
            eventAction: 'email_verified',
          });
        } catch (e) {
          this.verificationCodeError = get(e, 'response.data.errors.message');
        }

        this.isLoading = false;
      }
    },

    async login() {
      if (!this.$v.email.$invalid && !this.$v.password.$invalid) {
        this.emailError = '';
        this.passwordError = '';
        this.isLoading = true;

        try {
          const { data: { data: { encrypted_private_key: encryptedPrivateKey, wallet_address: walletAddress } } } = await login({ email: this.email });
          const privateKey = CryptoJS.AES.decrypt(encryptedPrivateKey, this.password).toString(CryptoJS.enc.Utf8);

          if (!privateKey) {
            this.passwordError = 'Invalid password';
            this.isLoading = false;
            return;
          }

          await this.importWallet(walletAddress, privateKey);

          this.$ga.event({
            eventCategory: 'login',
            eventAction: 'login_successful',
            eventLabel: 'email',
          });
        } catch (e) {
          if (get(e, 'response.data.errors.source') === 'email') {
            if (get(e, 'response.data.errors.error_key') === 'UNVERIFIED_EMAIL') {
              this.authBlock = 'verificationCode';
              this.emailRegister = this.email;
            } else {
              this.emailError = get(e, 'response.data.errors.message');
            }
          } else if (get(e, 'response.data.errors[0]')) {
            this.passwordError = get(e, 'response.data.errors[0].message');
          } else {
            this.passwordError = 'Invalid password';
          }

          this.isLoading = false;
        }
      }
    },

    async importWallet(address, privateKey) {
      if (!this.$store.getters['wallets/hasPrivateKeyWallet'](this.address)) {
        await this.$store.dispatch('wallets/importWallet', {
          address,
          privateKey,
          type: PRIVATE_KEY_TYPE,
        });

        //     
        this.$ga.page({
          page: '/virtual/import',
          title: 'Wallet Was Imported',
          location: window.location.href,
        });
      } else {
        this.$store.dispatch('wallets/setActiveWallet', {
          address,
          type: PRIVATE_KEY_TYPE,
        });
      }

      this.goToTrading();
    },

    goToTrading() {
      this.$router.push({ name: 'futures' });
    },

    /**
     *    
     *    ,    ,    ,   
     */
    async loginWithMetamask() {
      this.isLoading = true;
      await this.$store.dispatch('wallets/loadMetamaskWallets');
      const wallet = this.$store.state.wallets.providedWallets[0];

      const cid = window.ga && window.ga.getAll() && window.ga.getAll()[0] ? window.ga.getAll()[0].get('clientId') : undefined;

      putGAClientID({ walletAddress: wallet.address, clientID: cid, ga: this.$ga, ref: this.referrerAddress, login_type: 'metamask' });
      this.$ga.event({
        eventCategory: 'login',
        eventAction: 'login_successful',
        eventLabel: 'metamask',
      });

      if (wallet) {
        //     
        this.setActiveWallet(wallet);
        this.goToTrading();
      } else {
        //     
        this.signUpOptionIndex = 0;
        this.currentVisibleBlock = 1;
      }
      this.isLoading = false;
    },

    /**
     *    Ledger
     *   Ledger ,    ,    ,   Ledger
     */
    async loginWithLedger() {
      this.isLoading = true;
      await this.$store.dispatch('wallets/loadLedgerWallets');
      const wallet = this.$store.state.wallets.ledgerWallets[0];

      const cid = window.ga && window.ga.getAll() && window.ga.getAll()[0] ? window.ga.getAll()[0].get('clientId') : undefined;

      putGAClientID({ walletAddress: wallet.address, clientID: cid, ga: this.$ga, ref: this.referrerAddress, login_type: 'ledger' });
      this.$ga.event({
        eventCategory: 'login',
        eventAction: 'login_successful',
        eventLabel: 'ledger',
      });

      if (wallet) {
        //     Ledger
        this.setActiveWallet(wallet);
        this.goToTrading();
      } else {
        //     Ledger
        this.signUpOptionIndex = 1;
        this.currentVisibleBlock = 2;
      }
      this.isLoading = false;
    },

    onCodePaste(e) {
      setTimeout(() => {
        this.code = [e.target.value[0], e.target.value[1], e.target.value[2], e.target.value[3]];
      }, 0);
    },

    onCodeKeyUp(e, code) {
      // this[`code${code}`] = e.target.value[e.target.value.length - 1];

      const charCode = (e.which) ? e.which : e.keyCode;
      if ((charCode >= 48 && charCode <= 57) || (charCode >= 96 && charCode <= 105)) {
        this.$refs[`code${+code + 1}`].focus();
      }

      if (charCode === 13) {
        this.verifyEmail();
      }
    },
  },

  components: {
    EIcon,
    EButton,
    EPreloader,
  },

  mixins: [validationMixin],
};
</script>

