    .e-icon svg {
      top: 50%;
      transform: translate(0, -50%);

      path, rect {
        transition: all .15s ease-in-out;
      }
    }
  }

  &__form-validation {
    display: flex;
    align-items: center;
    color: #DC4727;
    font-size: 12px;
    font-weight: 500;
    margin-top: -4px;
    margin-bottom: 8px;

    &.verificationCode {
      justify-content: center;
      margin-top: 0;
    }

    &.form {
      margin-top: 14px;
      margin-bottom: 3px;
    }

    .e-icon {
      width: 14px;
      height: 14px;
      margin-right: 5px;
    }
  }

  &__form-submit {
    background: #00DB9C;
    box-shadow: 0 8px 18px 0 rgba(10,89,67,0.10);
    font-weight: 500;
    font-size: 18px;
    color: #FFFFFF;
    letter-spacing: 0;
    text-align: center;
    line-height: 26px;
    width: 100%;
    height: 50px;
    border-radius: 12px;
    transition: all .15s ease-in-out;

    &[disabled] {
      background: #adbcbb;

      &:hover {
        background: #adbcbb;
      }
    }

    &:hover {
      background: #14cf89;
    }

    &:active {
      transform: translate(0, 1px);
    }
  }

  &__form-to-register {
    text-align: center;
    margin-top: 20px;
  }

  &__form-to-register-label {
    font-weight: 500;
    font-size: 13px;
    color: #6B7397;
    letter-spacing: 0;
    text-align: center;
    line-height: 12px;
  }

  &__form-to-register-link {
    display: block;
    padding: 4px 0 0;
    font-weight: 500;
    font-size: 18px;
    color: #00DB9C;
    letter-spacing: 0;
    text-align: center;
    line-height: 26px;
    cursor: pointer;
    transition: all .15s ease-in-out;

    &:hover {
      color: #14cf89;
    }
  }

  &__variants {
    position: relative;
    background: #fff;
    width: calc(100% + 82px);
    transform: translate(-41px, 0);
    border-radius: 4px 4px 40px 40px;
    margin-top: 20px;
    margin-bottom: -26px;

    @include r(1200) {
      width: calc(100% + 52px);
      transform: translate(-26px, 0);
    }
  }

  &__variants-label {
    padding: 2px 5px;
    border-radius: 4px;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #CACCD3;
    font-weight: 500;
    font-size: 9px;
    color: #6B7397;
    letter-spacing: 1px;
    text-align: center;
    line-height: 12px;
    text-transform: uppercase;
  }

  &__variants-list {
    display: flex;
    align-items: stretch;
    height: 70px;
  }

  &__variants-list-item {
    flex: 0 0 33.33%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 9px;
    color: #6B7397;
    text-transform: uppercase;
    cursor: pointer;
    text-decoration: none;
    transition: all .15s ease-in-out;

    &:first-child {
      border-radius: 4px 0 0 40px;
    }

    &:last-child {
      border-radius: 0 4px 40px 0;
    }

    &:hover {
      background: #ECECEF;
    }
  }

  &__variants-list-icon {
    width: 18px;
    height: 18px;
    margin-bottom: 4px;
  }

  &__forget {
    display: flex;
    align-items: center;
    background: rgba(220,71,39,0.10);
    border: 1px solid rgba(220,71,39,0.40);
    font-weight: 500;
    font-size: 12px;
    color: #D65443;
    letter-spacing: -.25px;
    line-height: 14px;
    padding: 8px 16px;
    border-radius: 12px;
    margin-bottom: 12px;
    box-sizing: border-box;

    .e-icon {
      width: 12px;
      flex: 0 0 12px;
      height: 38px;
      margin-right: 10px;
    }
  }

  @keyframes bounce {
    80% { transform: scale(1); }
    90% { transform: scale(1.15); }
    100% { transform: scale(1); }
  }
}
</style>



