<template>
  <div class="the-how-it-works" id="howItWorks">
    <div class="the-how-it-works__waves-wrapper blue">
      <!--<e-icon :src="iconWave" class="the-how-it-works__waves-wrapper-item-wave wave-1" />-->
      <e-icon :src="iconWave" class="the-how-it-works__waves-wrapper-item-wave wave-2" />
      <e-icon :src="iconWave" class="the-how-it-works__waves-wrapper-item-wave wave-3" />
    </div>

    <div class="the-how-it-works__section-wrapper">
      <div class="the-how-it-works__headline">
        <div class="the-how-it-works__headline-side">
          <div class="the-how-it-works__headline-title">
            How it <span class="the-how-it-works__highlight">Works</span>?
          </div>
        </div>
        <div class="the-how-it-works__headline-content">
          <div class="the-how-it-works__headline-title-sub">
            <e-icon :src="iconDmex" class="the-how-it-works__headline-title-logo" />
            <div>
              <span class="the-how-it-works__highlight">Decentralized</span>
              <br>
              Mercantile <span class="the-how-it-works__highlight">Exchange</span>
            </div>
          </div>
          <div class="the-how-it-works__headline-text">
            <div class="the-how-it-works__headline-text-item">
              The first of its kind DEX (<span class="the-how-it-works__highlight">decentralized exchange</span>) for derivatives trading, <span class="the-how-it-works__highlight">D</span><span class="the-how-it-works__highlight-white">MEX</span> combines the speed and user experience of a traditional exchange with the total security of funds and anonymity of a DEX.
            </div>
            <div class="the-how-it-works__headline-text-item">
              <span class="the-how-it-works__highlight">D</span><span class="the-how-it-works__highlight-white">MEX</span> provides futures trading facilities for the most popular cryptocurrencies and tokens with <span class="the-how-it-works__highlight">up to 100x leverage, high liquidity, tight spreads</span> and <span class="the-how-it-works__highlight">decentralized price feeds</span>. All you need is ETH and a trading idea.
            </div>
          </div>
        </div>
      </div>

      <div class="the-how-it-works__flow">
        <div class="the-how-it-works__flow-ecosystem">
          The <span class="the-how-it-works__highlight">DMEX</span> ecosystem consists of four elements
        </div>

        <div class="the-how-it-works__flow-text">
          <div class="the-how-it-works__flow-headline">
            The information <span class="the-how-it-works__highlight">flow</span>
          </div>

          <div class="the-how-it-works__flow-list">
            <div class="the-how-it-works__flow-item" @mouseenter="setIllustrationHighlight('work-user-highlight')" @mouseleave="setIllustrationHighlight('')">
              <div class="the-how-it-works__flow-num">1</div>
              User signs the order through the DMEX Web App and sends it to the DMEX Data Servers.
            </div>
            <div class="the-how-it-works__flow-item" @mouseenter="setIllustrationHighlight('work-server-highlight')" @mouseleave="setIllustrationHighlight('')">
              <div class="the-how-it-works__flow-num">2</div>
              The order is matched against other orders in the orderbook.
            </div>
            <div class="the-how-it-works__flow-item" @mouseenter="setIllustrationHighlight('work-smart-highlight')" @mouseleave="setIllustrationHighlight('')">
              <div class="the-how-it-works__flow-num">3</div>
              The two matching orders are sent to the DMEX Smart Contract that verifies the authenticity of the signatures, the price condition and the respective user balances.
            </div>
            <div class="the-how-it-works__flow-item" @mouseenter="setIllustrationHighlight('work-positions-highlight')" @mouseleave="setIllustrationHighlight('')">
              <div class="the-how-it-works__flow-num">4</div>
              Two opposite positions are created on the smart contract, one Long and the other one Short. The respective collaterals are locked up on users accounts.
            </div>
            <div class="the-how-it-works__flow-item" @mouseenter="setIllustrationHighlight('work-oracle-highlight')" @mouseleave="setIllustrationHighlight('')">
              <div class="the-how-it-works__flow-num">5</div>
              Once the contract expires, the Oracle provides the snapshot of the price for the asset to the smart contract and closes both positions. The loser pays the winner. (The user can also close the position early by posting an opposite order).
            </div>
          </div>
        </div>

        <div class="the-how-it-works__flow-preview">
          <div class="the-how-it-works__flow-preview-illustration" :class="activeIllustrationHighLight">
            <e-icon :src="iconWorkIllustration"/>
          </div>
          <div class="the-how-it-works__flow-info webapp">
            <div class="the-how-it-works__flow-info-icon">
              <e-icon :src="iconWorkPlus" />
            </div>
            <div class="the-how-it-works__flow-info-title">
              Web App
            </div>

            <div class="the-how-it-works__flow-info-tooltip">
              <div class="the-how-it-works__flow-info-tooltip-headline">
                <e-icon :src="iconWorkArrow" />
                Web App
              </div>
              The DMEX website runs as a standalone application on your computer. It only receives orders and statistics from the data servers. The web application stores your private keys in your browser or connects to your external Ethereum Wallet. Your private keys are never sent to our servers. The web application signs the orders you confirm and sends the orders with signatures to the data server. The signatures can prove that the order was signed by the owner of the wallet, but the private keys cannot be derived from the signatures.
            </div>
          </div>

          <div class="the-how-it-works__flow-info dataServers">
            <div class="the-how-it-works__flow-info-icon">
              <e-icon :src="iconWorkPlus" />
            </div>
            <div class="the-how-it-works__flow-info-title">
              Data Servers
            </div>

            <div class="the-how-it-works__flow-info-tooltip left">
              <div class="the-how-it-works__flow-info-tooltip-headline">
                <e-icon :src="iconWorkArrow" />
                Data Servers
              </div>
              Orders and exchange statistics, although accesible directly from the blockchain, can take a long time to be extracted. Our servers store the exchange information in order to provide it at lightning fast speeds to the users.
            </div>
          </div>

          <div class="the-how-it-works__flow-info blockchain">
            <div class="the-how-it-works__flow-info-icon">
              <e-icon :src="iconWorkPlus" />
            </div>
            <div class="the-how-it-works__flow-info-title">
              Blockchain
            </div>

            <div class="the-how-it-works__flow-info-tooltip left">
              <div class="the-how-it-works__flow-info-tooltip-headline">
                <e-icon :src="iconWorkArrow" />
                Blockchain
              </div>
              All user funds are stored in the DMEX smart contract running on the Ethereum blockchain. All trades are recorded on the blockchain. Trades move funds from one user account to another. This way, DMEX is a fully transparent system. All trades and balances are publicly available.
            </div>
          </div>

          <div class="the-how-it-works__flow-info externalOracle">
            <div class="the-how-it-works__flow-info-icon">
              <e-icon :src="iconWorkPlus" />
            </div>
            <div class="the-how-it-works__flow-info-title">
              External Oracle
            </div>

            <div class="the-how-it-works__flow-info-tooltip">
              <div class="the-how-it-works__flow-info-tooltip-headline">
                <e-icon :src="iconWorkArrow" />
                External Oracle
              </div>
              The external oracle performs the duty of providing the settlement prices for the closed or expired contracts. The Oracle is operated by a third party project
              <a href="https://provable.xyz" target="_blank">Provable.xyz</a> and is provably decentralized. Provable is running a preprogrammed black box machine in the Amazon AWS datacenter that responds to requests to provide contents of specific URLs and returns them to the requesting contract along with the proof of authenticity.
            </div>
          </div>
        </div>

      </div>

    </div>
  </div>
</template>

<script>
import EIcon from '@/components/EIcon';

import iconMail from '@/icons/e-icon__mail.svg';
import iconWorkIllustration from '@/icons/e-icon__work-illustration.svg';
import iconWorkPlus from '@/icons/e-icon__work-plus.svg';
import iconWorkArrow from '@/icons/e-icon__work-arrow.svg';
import iconWave from '@/icons/e-icon__wave.svg';
import iconDmex from '@/icons/e-icon__dmex.svg';

export default {
  name: 'the-how-it-works',

  data() {
    return {
      activeIllustrationHighLight: '',

      // Icons
      iconMail,
      iconWorkIllustration,
      iconWorkPlus,
      iconWorkArrow,
      iconWave,
      iconDmex,
    };
  },

  methods: {
    setIllustrationHighlight(className) {
      this.activeIllustrationHighLight = className;
    },
  },

  components: {
    EIcon,
  },
};
</script>

