<template>
  <div class="page-affiliate-history">
    <transition
      name="transition-header"
      :duration="{ enter: 2000, leave: 600 }"
    >
      <div
        v-if="isPopupShowing"
        class="page-news__header"
      >
        <the-header-inner/>
      </div>
    </transition>

    <div class="page-affiliate-history__root">
      <div id="page-affiliate-history__wrapp">
        <the-header-new />

        <div class="page-affiliate-history__main">
          <div class="page-affiliate-history__main-content">

            <e-heading
              title="Affiliate history"
              size="medium"
              hasBack
            />

            <div v-if="userAddress" class="page-affiliate-history__affiliate-stats">
              <div class="page-affiliate-history__affiliate-stats-item link">
                <e-icon :src="$options.icons.iconLink" class="page-affiliate-history__affiliate-stats-item-icon" />

                <div class="page-affiliate-history__affiliate-stats-content">
                  <span class="page-affiliate-history__affiliate-stats-label">Affiliate link</span>

                  <div class="page-affiliate-history__affiliate-stats-link">
                    http://dmex.app/a/{{this.ref}}

                    <div
                      class="page-affiliate-history__affiliate-stats-copy"
                      :class="{active: copied}"
                      v-clipboard:copy="`http://dmex.app/a/${this.ref}`"
                      v-clipboard:success="onRefCopy"
                    >
                      <e-icon :src="$options.icons.iconCopy"/>
                      <e-icon :src="$options.icons.iconLike"/>
                    </div>
                  </div>
                </div>
              </div>
              <div class="page-affiliate-history__affiliate-stats-item users">
                <e-icon :src="$options.icons.iconUsers" class="page-affiliate-history__affiliate-stats-item-icon" />

                <div class="page-affiliate-history__affiliate-stats-content">
                  <span class="page-affiliate-history__affiliate-stats-label">Users Referred</span>
                  <div class="page-affiliate-history__affiliate-stats-value">{{usersReferred}}</div>
                </div>
              </div>
              <div class="page-affiliate-history__affiliate-stats-item commission">
                <div class="page-affiliate-history__affiliate-commission">
                  <div class="page-affiliate-history__affiliate-commission-content">
                    <div class="page-affiliate-history__affiliate-stats-label">Commission Balance</div>
                    <p class="page-affiliate-history__affiliate-commission-content-text">The commission will be credited to your DMEX contract balance. The transaction Gas Fee will be deducted from the amount.</p>
                  </div>

                  <div class="page-affiliate-history__affiliate-commission-balance">
                    <div v-for="balance in commissionBalances" :key="balance.token_address" class="page-affiliate-history__affiliate-commission-balance-item">
                      <div class="page-affiliate-history__affiliate-commission-balance-currency">{{balance.token_symbol}}</div>
                      <div class="page-affiliate-history__affiliate-commission-balance-value">
                        <e-big-number
                          :value="balance.token_amount"
                          :decimals="balance.token_decimals"
                          :decimalPlaces="4"
                        />
                      </div>
                      <button type="button" class="page-affiliate-history__affiliate-commission-balance-button" @click="onRelease(balance.token_address)">Collect</button>
                    </div>
                  </div>
                </div>

              </div>

            </div>

            <the-affiliate-history />
          </div>
        </div>
        <the-footer/>
      </div>
    </div>

    <!-- popup and menu -->
    <the-popup v-if="isPopupShowing"/>
    <the-menu-sidebar v-if="isPopupShowing"/>
    <!-- popup and menu -->
  </div>
</template>

<script>
/**
 * Component:   
 *
 * @version 2.0.0
 */

import CryptoJS from 'crypto-js';
import { mapGetters } from 'vuex';

import { getAffiliateStats, affiliateRelease } from '@/api/backend/affiliate';

// Components
import TheAffiliateHistory from '@/containers/blocks/TheAffiliateHistory';
import TheFooter from '@/containers/blocks/TheFooter';
import TheHeaderInner from '@/containers/blocks/TheHeaderInner';
import TheHeaderNew from '@/containers/blocks/TheHeaderNew';
import TheMenuSidebar from '@/containers/blocks/TheMenuSidebar';
import ThePopup from '@/containers/blocks/ThePopup';
import EHeading from '@/components/EHeading';
import EBigNumber from '@/components/EBigNumber';
import EIcon from '@/components/EIcon';

import iconLink from '@/icons/e-icon__link4.svg';
import iconUsers from '@/icons/e-icon__users.svg';
import iconCopy from '@/icons/e-icon__copy.svg';
import iconLike from '@/icons/e-icon_like.svg';


export default {
  name: 'page-affiliate-history',

  data() {
    return {
      historyPage: [],
      // activeTab: TAB_FUTURES_TRADING,
      historyPageLoading: false,
      copied: false,
      usersReferred: 'N/A',
      commissionBalances: [],
    };
  },

  icons: {
    iconLink,
    iconUsers,
    iconCopy,
    iconLike,
  },

  computed: {
    ref() {
      return String(CryptoJS.MD5(this.userAddress)).substring(0, 7);
    },

    ...mapGetters({
      userAddress: 'wallets/activeWalletAddress',
      isPopupShowing: 'ui/isPopupShowing',
    }),
  },

  async mounted() {
    this.$ga.page(this.$router);
    this.getAffiliateStats();
  },

  methods: {
    async getAffiliateStats() {
      const { data: { data } } = await getAffiliateStats(this.userAddress);
      this.usersReferred = data.users_referred;
      this.commissionBalances = data.commission_balances;
    },

    onRefCopy() {
      if (!this.copied) {
        this.copied = true;

        setTimeout(() => {
          this.copied = false;
        }, 1500);
      }
    },

    async onRelease(tokenAddress) {
      try {
        await affiliateRelease(this.userAddress, tokenAddress);
        this.$store.commit('ui/ADD_NOTIFICATION', {
          status: 'success',
          headline: 'Success',
          message: 'Successfully released',
        });
      } catch (e) {
        this.$store.commit('ui/ADD_NOTIFICATION', {
          status: 'error',
          headline: 'Error',
          message: e.response.data.errors.map(err => err.message).join(' '),
        });
      }
    },
  },

  components: {
    TheAffiliateHistory,
    TheFooter,
    TheHeaderInner,
    TheHeaderNew,
    TheMenuSidebar,
    ThePopup,
    EHeading,
    EBigNumber,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.page-affiliate-history {
  $this: &;

  .the-header-new {
    background-color: #0f0d44;
  }

  &__root {
    font-weight: 500;
    background-color: #f2f2f2;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    overflow-y: auto;
  }

  &__wrapp {
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    height: 100%;
  }

  &__header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 78px;
    z-index: 102;
  }

  // MAIN section
  // ==================================================
  &__main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #f6f7fb;
    margin-top: -120px;
    padding: 120px 30px 0;
    box-sizing: border-box;
    z-index: 0;
  }

  &__main-content {
    width: 100%;
    max-width: 1200px;
    padding: 70px 0 100px;
    box-sizing: border-box;
    margin: 0 auto;
    position: relative;

    @include r(1279) {
      padding-top: 30px;
    }
  }

  .e-panel-error.e-panel-error__dark {
    background: #fff;
  }

  .the-history {
    overflow-y: auto;
    min-height: 210px;
  }

  &__affiliate-link {
    span {
      color: #8D9098;
      font-weight: 400;
    }
  }

  &__affiliate-stats {
    display: flex;
    margin-top: 30px;
    margin-right: 14px;
    margin-bottom: 24px;

    @include r(1100) {
      margin-right: 0;
      flex-wrap: wrap;
    }
  }

  &__affiliate-stats-item {
    display: flex;
    align-items: center;
    flex: 1;
    padding: 20px 25px 20px 15px;
    min-height: 100px;
    box-sizing: border-box;
    border-radius: 4px;
    background: #fff;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.03);
    margin: 0 5px;

    &:first-child {
      margin-left: 0;
    }

    &:last-child {
      margin-right: 0;
    }

    &.link {
      flex: 0 0 340px;
      background: #040B48;

      #{$this}__affiliate-stats-label {
        color: #8383AC;
      }
    }

    &.users {
      flex: 0 0 220px;
    }

    &.commission {
      #{$this}__affiliate-stats-label {
        padding-bottom: 0;
      }
    }

    @include r(1100) {
      &.link,
      &.users,
      &.commission {
        flex: 0 0 100%;
        margin: 0 0 5px;
        padding: 12px 20px 12px 12px;
        min-height: 80px;

        &:last-child {
          margin: 0;
        }
      }
    }
  }

  &__affiliate-stats-item-icon.e-icon {
    width: 46px;
    height: 46px;
    margin-right: 12px;
  }

  &__affiliate-stats-label {
    display: block;
    font-size: 15px;
    color: #0D105B;
    letter-spacing: 0;
    line-height: 22px;
    padding-bottom: 4px;
  }

  &__affiliate-stats-value {
    font-size: 17px;
    color: #232323;
    line-height: 25px;
  }

  &__affiliate-stats-link {
    display: flex;
    font-size: 17px;
    align-items: center;
    color: #fff;

    @include r(1100) {
      font-size: 14px;
    }
  }

  &__affiliate-stats-copy {
    margin-left: 10px;
    cursor: pointer;
    transition: opacity .1s ease-in-out;
    width: 15px;
    height: 20px;
    overflow: hidden;

    &:hover {
      opacity: .8;
    }

    .e-icon {
      transform: translate(0, 0);
      transition: transform .15s linear;

      &:nth-child(2) {
        width: 18px;
      }
    }

    &.active {
      .e-icon {
        transform: translate(0, -100%);
      }
    }
  }

  &__affiliate-commission-content-text {
    font-weight: 500;
    font-size: 12px;
    color: #8383AC;
    letter-spacing: 0;
    line-height: 15px;
    margin: 0;
  }

  &__affiliate-commission {
    display: flex;
    align-items: center;

    @include r(640) {
      flex-wrap: wrap;
    }
  }

  &__affiliate-commission-content {
    @include r(640) {
      flex: 0 0 100%;
      margin-bottom: 12px;
    }
  }

  &__affiliate-commission-balance {
    flex: 0 0 190px;
    margin-left: 18px;

    @include r(640) {
      flex: 0 0 100%;
      margin-left: 0;
    }
  }

  &__affiliate-commission-balance-item {
    display: flex;
    align-items: center;
    background: rgba(196,204,238,0.20);
    border: 1px solid rgba(107,115,151,0.13);
    border-radius: 4px;
    height: 34px;
    overflow: hidden;
    margin-bottom: 5px;

    &:last-child {
      margin-bottom: 0;
    }
  }
  &__affiliate-commission-balance-currency {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    font-size: 11px;
    color: #8D9098;
    background: rgba(196,204,238,0.20);
    line-height: 18px;
    width: 44px;
  }
  &__affiliate-commission-balance-value {
    padding: 0 15px;

    .e-big-number {
      font-size: 17px;
      color: #232323;
      line-height: 18px;
    }
  }
  &__affiliate-commission-balance-button {
    margin-left: auto;
    padding: 3px 8px;
    margin-right: 10px;
    background: #0122E7;
    box-shadow: 0 2px 5px 0 rgba(4,11,72,0.20);
    font-weight: 500;
    border-radius: 3px;
    font-size: 11px;
    color: #FFFFFF;
    letter-spacing: 0;
    text-align: center;
    transition: background-color .15s ease-in-out;

    &:hover {
      background: #2a5bf0;
    }

    &:active {
      transform: translate(0, 1px);
    }
  }

  .e-heading__back {
    @include r(1320) {
      top: 10px;
    }

    @include r(767) {
      top: 6px;
    }

    @include r(640) {
      top: 3px;
    }
  }
}

</style>



