<template>
  <div class="page-history">
    <transition
      name="transition-header"
      :duration="{ enter: 2000, leave: 600 }"
    >
      <div
        v-if="isPopupShowing"
        class="page-news__header"
      >
        <the-header-inner/>
      </div>
    </transition>

    <div class="page-history__root">
      <div id="page-history__wrapp">
        <the-header-new />

        <div class="page-history__main">
          <div class="page-history__main-content">

            <e-heading
              title="Transaction history"
              size="medium"
              hasBack
            />

            <the-history />
          </div>
        </div>
        <the-footer/>
      </div>
    </div>

    <!-- popup and menu -->
    <the-popup v-if="isPopupShowing"/>
    <the-menu-sidebar v-if="isPopupShowing"/>
    <!-- popup and menu -->
  </div>
</template>

<script>
/**
 * Component:   
 *
 * @version 2.0.0
 */


import { mapGetters } from 'vuex';

// Components
import TheHistory from '@/containers/blocks/TheHistory';
import TheFooter from '@/containers/blocks/TheFooter';
import TheHeaderInner from '@/containers/blocks/TheHeaderInner';
import TheHeaderNew from '@/containers/blocks/TheHeaderNew';
import TheMenuSidebar from '@/containers/blocks/TheMenuSidebar';
import ThePopup from '@/containers/blocks/ThePopup';
import EHeading from '@/components/EHeading';


export default {
  name: 'page-history',

  computed: {
    ...mapGetters({
      isPopupShowing: 'ui/isPopupShowing',
    }),
  },

  async mounted() {
    this.$ga.page(this.$router);
  },

  components: {
    TheHistory,
    TheFooter,
    TheHeaderInner,
    TheHeaderNew,
    TheMenuSidebar,
    ThePopup,
    EHeading,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.page-history {
  $this: &;

  .the-header-new {
    background-color: #0f0d44;
  }

  &__root {
    font-weight: 500;
    background-color: #f2f2f2;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    overflow-y: auto;
  }

  &__wrapp {
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    height: 100%;
  }

  &__header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 78px;
    z-index: 102;
  }

  // MAIN section
  // ==================================================
  &__main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #f6f7fb;
    margin-top: -120px;
    padding: 120px 30px 0;
    box-sizing: border-box;
    z-index: 0;
  }

  &__main-content {
    width: 100%;
    max-width: 1200px;
    padding: 70px 0 100px;
    box-sizing: border-box;
    margin: 0 auto;
    position: relative;

    @include r(1279) {
      padding-top: 30px;
    }
  }

  .e-panel-error.e-panel-error__dark {
    background: #fff;
  }

  .the-history {
    overflow-y: auto;
    min-height: 210px;
  }

  .e-heading__back {
    @include r(1320) {
      top: 10px;
    }

    @include r(767) {
      top: 6px;
    }

    @include r(640) {
      top: 3px;
    }
  }
}

</style>



