<template>
  <div
    class="page-help"
    :class="{ 'is-popup-showing': isPopupShowing }"
  >
    <transition
      name="transition-header"
      :duration="{ enter: 2000, leave: 600 }"
    >
      <div
        v-if="isPopupShowing"
        class="page-news__header"
      >
        <the-header-inner/>
      </div>
    </transition>

    <div class="page-help__root">
      <div id="page-help__wrapp">
        <the-header-new />

        <div class="page-help__main">
          <div class="page-help__main-content">
            <div class="page-help__main-sidebar">
              <a href="#" class="page-help__link" v-scroll-to="{ el: '#accounts', container: '.page-help__root' }">Accounts</a>
              <a href="#" class="page-help__link" v-scroll-to="{ el: '#createNewWallet', container: '.page-help__root' }">Create New Wallet</a>
              <a href="#" class="page-help__link" v-scroll-to="{ el: '#importExistingWallet', container: '.page-help__root' }">Import an existing Ethereum wallet</a>
              <a href="#" class="page-help__link" v-scroll-to="{ el: '#connectMetamask', container: '.page-help__root' }">Connect MetaMask</a>
              <a href="#" class="page-help__link" v-scroll-to="{ el: '#connectLedger', container: '.page-help__root' }">Connect Ledger Nano S/Trezor</a>
              <a href="#" class="page-help__link" v-scroll-to="{ el: '#depositFunds', container: '.page-help__root' }">Deposit funds to DMEX</a>
              <a href="#" class="page-help__link" v-scroll-to="{ el: '#placingOrder', container: '.page-help__root' }">Placing an order</a>
              <a href="#" class="page-help__link" v-scroll-to="{ el: '#withdrawFunds', container: '.page-help__root' }">Withdrawing funds from DMEX</a>
            </div>

            <div class="page-help__text">
              <div class="page-help__heading-main">
                <div @click="$router.go(-1)" class="page-help__back"></div>
                Help
              </div>

              <div class="page-help__text-section">
                <h2 class="page-help__headline" id="accounts">Accounts</h2>
                <p>DMEX uses Ethereum wallets as accounts for trading. You do not need to provide your email address, username or password. Your funds are secured by your wallet private keys.</p>
                <p>You can connect any existing Ethereum wallet to the DMEX interface or create a brand-new wallet straight from the website.</p>
              </div>
              <div class="page-help__text-section">
                <h2 class="page-help__headline" id="createNewWallet">Create New Wallet</h2>
                <p>In order to create a fresh Ethereum wallet, press the Create New Wallet button in the top right corner of the trading interface:</p>
                <img src="/static/img/help_connect.png" class="page-help__image">
                <p>You will be redirected to a page that contains your new wallet address and private key. Copy this information and store it somewhere safe. You can share your wallet address with other people if you wish so, but NEVER disclose your private key. Anyone knowing your private key can access your funds.</p>
                <img src="/static/img/help_wallet.png" class="page-help__image" style="width: 100%; max-width: 600px;">
                <p>We recommend storing the private keys in an encrypted file or printing it out on a piece of paper and storing it in a safe place.</p>
                <p>Note: New wallets are generated directly in your browser, not on our servers. The private keys are never sent to our servers, so we never have access to your funds. Thats the point of decentralization. We will NEVER ask for your private keys for ANY reason. If someone impersonating our staff asks for your private keys, you are dealing with a fraudster.</p>
              </div>
              <div class="page-help__text-section">
                <h2 class="page-help__headline" id="importExistingWallet">Import an existing Ethereum wallet</h2>
                <p>If you already have an Ethereum wallet you can simply connect it to the DMEX interface. Press the Connect button in the top-right corner of the trading page:</p>
                <img src="/static/img/help_connect.png" class="page-help__image">
                <p>On the next page, choose how you wish to unlock your wallet. You have the option of using:</p>
                - An address and private key (use this method if you have created your wallet on DMEX)
                <br>
                - Keystore file
                <br>
                - Mnemonic phrase
                <p>Enter the relevant information in the input fields and press Import Wallet</p>
                <img src="/static/img/help_import.png" class="page-help__image" style="width: 100%; max-width: 600px;">
                <p>If the information is correct you will see your wallet address in the top-right corner of the trading interface:</p>
                <img src="/static/img/help_wallet-num.png" class="page-help__image">
                <p>The private keys are stored on your browser and are not sent to our servers. If you wish to remove the private key from the browser, press the Forget wallet button:</p>
                <img src="/static/img/help_wallet-forget.png" class="page-help__image">
              </div>
              <div class="page-help__text-section">
                <h2 class="page-help__headline" id="connectMetamask">Connect MetaMask</h2>
                <p>To connect your MetaMask wallet, press the Connect button in the top-right corner of the trading interface.</p>
                <img src="/static/img/help_connect.png" class="page-help__image">
                <p>On the bottom of the next page click on the Log in with MetaMask link:</p>
                <img src="/static/img/help_login-metamask.png" class="page-help__image">
                <p>Make sure your MetaMask wallet is unlocked and click Confirm when you are asked if you wish to connect to DMEX.App.</p>
                <p>Once connection is successful, you will see your MetaMask wallet address in the top-right corner of the trading interface.</p>
                <img src="/static/img/help_metamask-wallet.png" class="page-help__image">
                <p>When creating/cancelling orders or requesting withdrawals, a MetaMask window will popup asking you to sign the respective message. Your private keys are stored in MetaMask and only signatures are sent to our servers. The DMEX website has no access to the private keys in your MetaMask wallet.</p>
              </div>
              <div class="page-help__text-section">
                <h2 class="page-help__headline" id="connectLedger">Connect Ledger Nano S/Trezor</h2>
                <p>To connect your Ledger Nano S wallet or Trezor, install the MetaMask extension on your browser and unlock it. Click on your account logo in the top right corner of the MetaMask interface</p>
                <img src="/static/img/help_ledger-connect.png" class="page-help__image">
                <p>In the next popup select Connect Hardware Wallet</p>
                <img src="/static/img/help_ledger-connect-hardware.png" class="page-help__image">
                <p>You will then be redirected to a page where your will be able to connect your Ledger or Trezor device.</p>
                <img src="/static/img/help_ledger-new.png" class="page-help__image">
                <p>Follow the instructions to complete the connection. Once done, follow the instructions to connect MetaMask to DMEX.</p>
              </div>
              <div class="page-help__text-section">
                <h2 class="page-help__headline" id="depositFunds">Deposit funds to DMEX</h2>
                <p>In order to deposit funds to DMEX you need to have ETH on your wallet. To get ETH on your wallet, just send it to the wallet address shown in the top-right corner of the trading interface. Use the Copy button to copy the full address:</p>
                <img src="/static/img/help_wallet-num.png" class="page-help__image">
                <p>Once you have ETH in your wallet balance, you will see them in the Balance section of the interface:</p>
                <img src="/static/img/help_wallet-balance.png" class="page-help__image"  style="width: 100%; max-width: 500px;">
                <p>What is shown in the Wallet Balance is the balance on your wallet. This fund cannot be accessed by DMEX contract. In order to be able to trade, you need to deposit funds from your Wallet Balance to your Contract balance.</p>
                <p>To do that, click on the right arrow as shown:</p>
                <img src="/static/img/help_wallet-balance-deposit.png" class="page-help__image" style="width: 100%; max-width: 500px;">
                <p>In the popup enter the amount of ETH you wish to deposit and press the Deposit ETH button. Optionally you can set up a higher Gas Price in order to have the deposit confirmed faster. If you dont know what Gas Price is, just leave the default value.</p>
                <img src="/static/img/help_deposit-popup.png" class="page-help__image">
                <p>Wait for the transaction to be confirmed on the blockchain. This may take a couple of minutes. Once the deposit is confirmed you will see the funds in your Contract balance.</p>
              </div>
              <div class="page-help__text-section">
                <h2 class="page-help__headline" id="placingOrder">Placing an order</h2>
                <p>1. Select the asset you wish to trade from the main dropdown:</p>
                <img src="/static/img/help_order1.png" class="page-help__image" style="width: 100%; max-width: 500px;">
                <p>2. Go to the order box and select the whether you want to place a Buy order (go long) or a Sell order (go short):</p>
                <img src="/static/img/help_order2.png" class="page-help__image">
                <p>3. Select the desired Margin level for your position. The higher the margin, the less collateral you have to provide, but also the closer the liquidation price. Choose carefully.</p>
                <img src="/static/img/help_order3.png" class="page-help__image">
                <p>A 3x margin means that you will have to provide only a third of the value of the order as collateral.</p>

                <p>4. Select the desired expiration for your position. Expiration determines when your position will be settled:</p>
                <img src="/static/img/help_order4.png" class="page-help__image">
                <p>If you select 1 Day, the position will be automatically closed in 24 hours using the price from the price source provided by our Oracle. Note: you dont have to wait for the position to expire, you can close it at any time by clicking the Close button in the Positions table.</p>

                <p>5. Enter the amount and price for the order in terms of the asset being purchased. If you wish to buy BTC, enter the amount in BTC (e.g. 0.2) and the price for 1 BTC. You can click on an order in the orderbook below to autocomplete the amount and price values.</p>
                <img src="/static/img/help_order5.png" class="page-help__image">

                <p>Below you will see the following information:</p>
                <p>
                  - Price Source: This is the external price source on which your position will be settled if left to expire.
                  <br>
                  - Collateral: This is the amount of ETH that will be locked in your account for the duration of the position. This amount represents your maximum potential loss. If you make a loss on the position, this loss will be taken out of the collateral.
                  <br>
                  - Take Profit Price: If the price of the asset on the Price Source reaches the Take Profit Price, your position will automatically be closed and you will be credited with your profit.
                  <br>
                  - Liquidation Price: The price at which your position will be liquidated and you will lose all the collateral. Positions are liquidated only based on the price form the Price Source provided by a decentralized Oracle. Prices traded on DMEX cannot liquidate your position.
                </p>

                <p>6. Once you are happy with the order, press the BUY/SELL button. You will be prompted with a popup to confirm your order. Check the information again and press Confirm Order</p>
                <img src="/static/img/help_order6.png" class="page-help__image">
                <p>If you use MetaMask, you will be asked to sign the order, otherwise the order will be signed in the background and sent to DMEX.</p>

                <p>7. You will be able to see all your orders in the Active Orders and Order History tabs below the chart.</p>
              </div>

              <div class="page-help__text-section">
                <h2 class="page-help__headline" id="withdrawFunds">Withdrawing funds from DMEX</h2>
                <p>1. Go to the Balance section of the trading interface and click on the left arrow:</p>
                <img src="/static/img/help_withdraw.png" class="page-help__image">

                <p>2. In the popup enter the amount of ETH you wish to withdraw and press the Withdraw ETH button:</p>
                <img src="/static/img/help_withdraw-popup.png" class="page-help__image">
                <p>Allow for a couple of minutes for your withdrawal to be recorded on the blockchain. Once confirmed you will see the withdrawn amount in your Wallet Balance.</p>

              </div>
            </div>
          </div>
        </div>
        <the-footer/>
      </div>
    </div>

    <!-- popup and menu -->
    <the-popup v-if="isPopupShowing"/>
    <the-menu-sidebar v-if="isPopupShowing"/>
    <!-- popup and menu -->
  </div>
</template>

<script>
/**
 * Component:  
 *
 * @version 2.0.0
 */


import { mapGetters } from 'vuex';

// Components
import TheFooter from '@/containers/blocks/TheFooter';
import TheHeaderInner from '@/containers/blocks/TheHeaderInner';
import TheHeaderNew from '@/containers/blocks/TheHeaderNew';
import TheMenuSidebar from '@/containers/blocks/TheMenuSidebar';
import ThePopup from '@/containers/blocks/ThePopup';


export default {
  name: 'page-aboutus',

  data() {
    return {};
  },

  computed: {
    ...mapGetters({
      isPopupShowing: 'ui/isPopupShowing',
    }),
  },

  async mounted() {
    this.$ga.page(this.$router);
  },

  components: {
    TheFooter,
    TheHeaderInner,
    TheHeaderNew,
    TheMenuSidebar,
    ThePopup,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.page-help {
  $this: &;

  .the-header-new {
    background-color: #0f0d44;
  }

  &__root {
    font-weight: 500;
    background-color: #f2f2f2;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    overflow-y: auto;
  }

  &__wrapp {
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    height: 100%;
  }

  &__header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 78px;
    z-index: 102;
  }

  // MAIN section
  // ==================================================
  &__main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #f2f2f2;
    margin-top: -120px;
    padding: 120px 50px 0;
    box-sizing: border-box;
    z-index: 0;
  }

  &__main-content {
    width: 100%;
    max-width: 1200px;
    padding: 50px 0;
    box-sizing: border-box;
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__heading-main {
    position: relative;
    color: #0d105b;
    font-size: 34px;
    font-weight: normal;
    margin-bottom: 20px;

    @include r(640) {
      line-height: 43px;
    }
  }

  &__heading-description {
    color: #000;
    font-size: 23px;
    line-height: 36px;
  }

  &__headline {
    color: #000;
    font-size: 23px;
    font-weight: 400;
    line-height: 36px;
    margin-bottom: 10px;
  }

  &__text-section {
    padding-bottom: 18px;
  }

  &__text {
    max-width: 1000px;
    flex: 1;

    p {
      color: #000;
      opacity: 0.6;
      font-size: 16px;
      line-height: 24px;
      font-weight: normal;
      margin: 10px 0;
    }

    @include r(767) {
      flex: 0 0 100%;
    }
  }

  &__image {
    margin-bottom: 6px;
    max-width: 100%;
  }

  &__main-sidebar {
    padding-top: 11px;
    width: 25%;
    padding-right: 40px;
    box-sizing: border-box;
    align-self: flex-start;
    position: sticky;
    top: 0;

    @include r(1000) {
      width: 230px;
      flex: 0 0 230px;
    }

    @include r(767) {
      width: 100%;
      flex: 0 0 100%;
      margin-bottom: 20px;
      position: relative;
    }
  }

  &__main-article {
    width: 75%;
    margin-right: 120px;
    position: relative;
  }

  &__back {
    position: absolute;
    display: inline-block;
    width: 17px;
    height: 24px;
    cursor: pointer;
    left: -47px;
    top: 16px;

    @include r(768) {
      left: -32px;
    }

    @include r(640) {
      top: 11px;
    }

    &::before, &::after {
      display: block;
      position: absolute;
      content: '';
      width: 17px;
      height: 2px;
      background: #1f2ee9;
    }

    &::before {
      transform: rotate(-45deg);
      left: 0;
      top: 5px;
    }

    &::after {
      transform: rotate(45deg);
      left: 0;
      top: 16px;
    }
  }

  &__link {
    font-size: 15px;
    color: #1F2EE9;
    line-height: 21px;
    letter-spacing: 0.5px;
    transition: all 0.2s;
    cursor: pointer;
    text-decoration: none;
    display: block;
    padding: 8px 0;

    &.router-link-exact-active {
      color: #000;
    }

    &:hover {
      color: #000;
    }
  }
}

</style>



