<template>
  <div class="the-history-table">

    <div class="the-history-table__head">
      <div class="the-history-table__date the-history-table__column the-history-table__left-side">
        <div class="the-history-table__heading">
          Date
        </div>
      </div>
      <div class="the-history-table__operation the-history-table__column the-history-table__left-side">
        <div class="the-history-table__heading">
          Operation
        </div>
      </div>
      <div class="the-history-table__amount the-history-table__column the-history-table__left-side">
        <div class="the-history-table__heading">
          Amount
        </div>
      </div>
      <div class="the-history-table__price the-history-table__column the-history-table__left-side">
        <div class="the-history-table__heading">
          Price
        </div>
      </div>
      <div class="the-history-table__fees the-history-table__column the-history-table__left-side">
        <div class="the-history-table__heading">
          Fee
        </div>
      </div>
      <div class="the-history-table__net-amount the-history-table__column the-history-table__left-side">
        <div class="the-history-table__heading">
          Total
        </div>
      </div>
      <div class="the-history-table__status the-history-table__column the-history-table__right-side">
        <div class="the-history-table__heading">
          Status
        </div>
      </div>
    </div>
    <e-container-scroll
      v-if="history.length"
      class="the-history-table__content"
      ref="scroll"
      :settings="settings"
    >
      <div class="the-history-table__container">
        <div
          v-for="(item, index) in historyWithTokens"
          :key="index"
          class="the-history-table__item"
        >
          <e-history-table-item
            :date="item.createdAt"
            :action="item.action"
            :section="item.section"

            :token="item.amountToken.shortName"
            :decimals="item.amountToken.decimals"
            :amount="item.amount"
            :futuresAssetName="item.futuresAssetName"
            :futuresAssetDecimals="item.futuresAssetDecimals"
            :unit-price="item.unitPrice"
            :is-able-to-have-unit-price="item.isAbleToHaveUnitPrice"

            :fee-token="item.incomeToken.shortName"
            :fee-decimals="item.incomeToken.decimals"
            :fee="item.fee"

            :gas-fee-token="item.gasFeeToken && item.gasFeeToken.shortName"
            :gas-fee-decimals="item.gasFeeToken && item.gasFeeToken.decimals"
            :gas-fee="item.gasFee"

            :net-amount-token="item.incomeToken.shortName"
            :net-amount-decimals="item.incomeToken.decimals"
            :net-amount="item.netAmount"

            :transactionHash="item.transactionHash"
            :status="item.isConfirmed"
            :last-item="item.isLastOnPage"
          />
        </div>
      </div>
    </e-container-scroll>

    <e-panel-error
      v-else
      class="the-history-table__empty"
      :icon="iconNoHistory"
    >
      <template slot="title">
        <span v-if="!isAffiliate">You dont have any orders</span>
        <span v-if="isAffiliate">You dont have affiliate transactions</span>
      </template>
      <template slot="descr">
        <span v-if="!isAffiliate">
          Deposit some tokens or connect <br>a Metamask wallet with positive token balance
        </span>
        <span v-if="isAffiliate">
          <br><br>
        </span>
      </template>
    </e-panel-error>

  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import initial from 'lodash/initial';
import last from 'lodash/last';
import CryptoJS from 'crypto-js';

// Components
import EHistoryTableItem from '@/components/EHistoryTableItem';
import EContainerScroll from '@/components/EContainerScroll';
import EPanelError from '@/components/EPanelError';

// Icons
import iconNoHistory from '@/icons/e-icon_spider.svg';

export default {
  name: 'the-history-table',

  data() {
    return {
      settings: {
        wheelPropagation: true,
        suppressScrollX: true,
      },
    };
  },

  props: {
    history: {
      type: Array,
    },

    isAffiliate: {
      type: Boolean,
    },
  },

  computed: {
    historyWithTokens() {
      const history = this.history;
      if (history.length > 0) {
        const updatedLastElement = {
          ...last(history),
          isLastOnPage: true,
        };
        return [...initial(history), updatedLastElement];
      }
      return history;
    },

    ref() {
      return String(CryptoJS.MD5(this.userAddress)).substring(0, 7);
    },

    ...mapGetters({
      userAddress: 'wallets/activeWalletAddress',
    }),
  },

  created() {
    Object.assign(this, { iconNoHistory });
  },

  components: {
    EPanelError,
    EContainerScroll,
    EHistoryTableItem,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-history-table {
  $number-column-size: 17%;
  $number-column-padding: 30px;

  display: flex;
  flex-flow: column;

  &__head {
    height: 17px;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 0 13px 0 5px;
  }

  &__column {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    flex: none;
    font-weight: 800;
  }

  &__left-side {
    justify-content: flex-start;
  }

  &__right-side {
    justify-content: flex-end;
  }

  &__block {
    background-color: black;
    height: 2000px;
  }

  &__center-side {
    justify-content: center;
  }

  &__heading {
    font-size: 8px;
    text-transform: uppercase;
    color: $color-dark-blue;
    font-weight: 700;
  }

  &__date {
    flex: 0 0 11%;
    padding-left: 17px;
    box-sizing: border-box;
  }

  &__operation {
    flex: 0 0 8%;
  }

  &__amount {
    flex: 0 0 $number-column-size;
    justify-content: flex-end;
    padding-right: $number-column-padding;
    box-sizing: border-box;
  }

  &__price {
    flex: 0 0 $number-column-size;
    justify-content: flex-end;
    padding-right: $number-column-padding;
    box-sizing: border-box;
  }

  &__fees {
    flex: 0 0 $number-column-size;
    justify-content: flex-end;
    padding-right: $number-column-padding;
    box-sizing: border-box;
  }

  &__gas-fees {
    flex: 0 0 $number-column-size;
    justify-content: flex-end;
    padding-right: $number-column-padding;
    box-sizing: border-box;
  }

  &__net-amount {
    flex: 0 0 $number-column-size;
    justify-content: flex-end;
    padding-right: $number-column-padding;
    box-sizing: border-box;
  }

  &__status {
    flex: 0 0 auto;
    width: 11%;
    padding-right: 16px;
    box-sizing: border-box;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 7px;
    }
  }

  &__container {
    position: relative;
  }

  &__content {
    height: 100%;
    box-sizing: border-box;
    padding: 0 5px;
    width: calc(100% - 10px);
  }

  .e-panel-error {
    @include r(767) {
      width: calc(100vw - 50px);
    }
  }

  .e-panel-error__title {
    @include r(767) {
      font-size: 15px;
    }
  }

  &__empty {
    height: 100%;
    min-height: 250px;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.08);
    border-radius: 3px;
    margin: 0 15px 0 5px;
    background-color: #ffffff;

    .e-panel-error__title:before {
      width: 90px;
      left: 0;
      right: 0;
      margin-left: auto;
      margin-right: auto;

      @include r(767) {
        bottom: 4px;
      }
    }

    .e-panel-error__icon-wrapp {
      width: 90px;
      height: 90px;
    }

    .e-panel-error__icon {
      width: 90px;
      height: 90px;
    }
  }
}
</style>



