<template>
  <div class="e-contracts-box default">
    <div class="e-contracts-box__list">
      <!-- BEGIN: Exchange Contracts -->
      <div class="e-contracts-box__item e-contracts-box__item_exchange">
        <div class="e-contracts-box__textual">
          <div class="e-contracts-box__circle"
            :class="{
              'e-contracts-box__circle_current': isLatestExchangeContractActive === true,
              'e-contracts-box__circle_outdated': isLatestExchangeContractActive === false,
            }"
          >
            <div class="e-contracts-box__icon">
              <e-icon :src="iconExchangeContract"/>
            </div>
          </div>
          <div class="e-contracts-box__inner">
            <h2 class="e-contracts-box__title">Base Contract</h2>
            <div class="e-contracts-box__descr">The base DMEX contract. All user funds are stored here.</div>
          </div>
        </div>
        <div
          v-if="loaded"
          class="e-contracts-box__select"
        >
          <e-contract-select
            :contracts="exchangeContracts"
            v-model="exchangeContractId"
          />
        </div>
      </div>
      <!-- END: Exchange Contracts -->

      <!-- BEGIN: Affiliates Contract -->
      <div class="e-contracts-box__item e-contracts-box__item_affiliate" v-if="affiliateContracts.length > 0">
        <div class="e-contracts-box__textual">
          <div class="e-contracts-box__circle e-contracts-box__circle_current">
            <div class="e-contracts-box__icon e-contracts-box__icon_blue">
              <e-icon :src="iconAffiliatesContract"/>
            </div>
          </div>
          <div class="e-contracts-box__inner">
            <h2 class="e-contracts-box__title">Affiliates Contract</h2>
            <div class="e-contracts-box__descr">The contract that stores the client-affiliate pairings.</div>
          </div>
        </div>
        <div
          v-if="loaded"
          class="e-contracts-box__select"
        >
          <e-contract-select
            v-model="affiliateContractVersion"
            :contracts="affiliateContracts"
          />
        </div>
      </div>
      <!-- END: Affiliates Contract -->
    </div>
  </div>
</template>

<script>
/**
 * Components:       
 *
 * @version 1.0.2
 */

import { mapState, mapGetters } from 'vuex';
import { ETHERSCAN_URL } from '@/config';

// Components
import EButton from '@/components/EButton';
import ECircleIcon from '@/components/ECircleIcon';
import EContractSelect from '@/components/EContractSelect';
import EIcon from '@/components/EIcon';

// Icons
import iconAffiliatesContract from '@/icons/e-icon_affiliates-contract.svg';
import iconExchangeContract from '@/icons/e-icon_exchange-contract.svg';

export default {
  name: 'e-contracts-box',

  data() {
    return {
      /**
       *       
       */
      affiliateContractVersion: 1,
    };
  },

  computed: {
    ...mapState({
      loaded: state => state.contracts.contractsLoaded,
    }),

    ...mapGetters({
      isLatestExchangeContractActive: 'contracts/isLatestExchangeContractActive',
    }),

    /**
     *     
     */
    exchangeContractId: {
      get() {
        return this.$store.getters['contracts/activeMainContract'].id;
      },
      async set(newId) {
        const contract = this.exchangeContracts.find(item => item.id === newId);
        if (contract) {
          await this.$store.dispatch('contracts/changeActiveMainContract', contract);
        }
      },
    },

    /**
     *    
     */
    exchangeContracts() {
      return this.$store.getters['contracts/exchangeContracts'].map(item => ({
        ...item,
        url: this.contractUrl(item.contractAddress),
      }));
    },

    /**
     *     
     */
    affiliateContracts() {
      const affiliateContract = this.$store.getters['contracts/affiliateContract'];

      if (!affiliateContract) {
        return [];
      }

      return [
        {
          contractAddress: affiliateContract,
          isCurrent: true,
          version: 1,
          url: this.contractUrl(affiliateContract),
        },
      ];
    },
  },

  created() {
    Object.assign(this, {
      iconAffiliatesContract,
      iconExchangeContract,
    });
    this.$store.dispatch('contracts/loadContracts');
  },

  methods: {
    /**
     *  URL    
     */
    contractUrl(contractAddress) {
      return this.loaded && contractAddress ? `${ETHERSCAN_URL}/address/${contractAddress}` : '#';
    },
  },

  components: {
    EIcon,
    ECircleIcon,
    EButton,
    EContractSelect,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-contracts-box {
  $this: &;

  position: relative;
  margin-bottom: 40px;

  &.default {
    z-index: 9;
  }

  &__item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;

    &:not(:last-of-type) {
      margin-bottom: 62px;

      @include r(1200) {
        margin-bottom: 53px;
      }
    }

    &_exchange {
      z-index: 3;
    }

    &_token-list {
      z-index: 2;
    }

    &_affiliate {
      z-index: 1;
    }
  }

  &__textual {
    display: flex;
    flex: 0 0 500px;
    max-width: 500px;
    margin-bottom: 12px;

    @include r(640) {
      flex: 1 1 100%;
    }
  }

  &__inner {
    padding-left: 32px;
    padding-right: 20px;
    color: $color-dark-blue;

    @include r(1200) {
      padding-left: 22px;
    }
  }

  &__circle {
    background-color: $color-white;
    border-radius: 50%;
    box-shadow: $box-shadow;
    min-height: 60px;
    min-width: 60px;
    height: 60px;
    width: 60px;
    position: relative;

    &:before {
      content: '';
      position: absolute;
      bottom: 13px;
      right: 13px;
      border-radius: 50%;
      width: 10px;
      height: 10px;
      z-index: 1;
    }

    @include r(1200) {
      min-height: 50px;
      min-width: 50px;
      height: 50px;
      width: 50px;
    }
  }

  &__circle_current {
    &:before {
      background-color: $color-green;
    }
  }

  &__circle_outdated {
    &:before {
      background-color: $color-red;
    }
  }

  &__circle_full-width {
    background-color: transparent;

    #{$this}__icon {
      height: 100%;
      width: 100%;
    }
  }

  &__icon {
    position: absolute;
    top: 50%;
    left: 50%;
    min-height: 26px;
    min-width: 26px;
    transform: translate(-50%, -50%);

    .e-icon svg {
      fill: $color-green;
    }

    @include r(1200) {
      min-height: 20px;
      min-width: 20px;
    }
  }

  &__icon_blue {
    .e-icon svg {
      fill: $color-blue;
    }
  }

  &__title {
    font-size: 20px;
    font-weight: 600;
    margin: 0 0 5px;

    @include r(1200) {
      font-size: 16px;
      margin-bottom: -2px;
    }
  }

  &__descr {
    font-size: 16px;
    font-weight: 400;

    @include r(1200) {
      font-size: 14px;
    }
  }

  &__descr-link {
    color: $color-blue;
    text-decoration: none;
  }

  &__btn {
    min-width: 170px;
    position: relative;
    top: 15px;

    @include r(1200) {
      min-width: 145px;
      top: 10px;
    }
  }

  &__link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 60px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
  }

  &__select {
    padding-left: 70px;

    @include r(640) {
      padding-left: 0;
    }
  }

  .e-contract-select__placeholder {
    @include r(640) {
      width: 100%;
    }
  }

  .e-contract-select__placeholder .e-contract-select__hash {
    @include r(640) {
      font-size: 10px;
    }
  }
}
</style>



