/**
 * :      
 *
 * @param {string} adress -   
 * @param {number} [left=10] -  ,    
 * @param {number} [right=10] -  ,    
 * @returns {string}
 *
 * @version 1.0.0
 */
export default (address, left = 10, right = 10) => (address
  ? `${address.slice(0, left)}...${address.slice(address.length - right, address.length)}`
  : '');



